<template>
  <div id="informpage">
    <div id="informgroup">
      <div class="flexbox w-100 h-100"></div>
      <div class="infoFrame box w-100 h-100">
        <div class="flexbox w-100 h-100"></div>
        <div class="infoHeight flexbox w-100 h-100 flex-c">
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="h-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
            <div class="flexbox w-100 h-100 flex-c">
              <div class="flexbox w-100 h-100 position-r">
                <div class="back_btn pointer" @click="info_close(false)"></div>
                <div class="close_btn pointer" @click="info_close(false)"></div>
              </div>
              <div class="flexbox block w-100 h-100" style="flex: 3.5">
                <div id="infoitem" class="w-100 h-100 d-flex flex-c scrollbar"
                  :style="{ 'max-height': scrollbox_h + 'px' }" v-html="checkinfo()">
                </div>
              </div>

              <div class="flexbox w-100 h-100">
                <div class="flexbox w-100 h-100"></div>
                <div class="checkBtn flexbox w-100 h-100" v-show="infobool">
                  <button @click="openitem()">查看</button>
                </div>
                <div class="flexbox w-100 h-100"></div>
              </div>
            </div>
            <div class="h-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
          </div>
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>

    <div id="informitem" v-show="itembool">
      <div class="flexbox" style="flex: 2"></div>
      <div class="flexSet flexbox w-100 h-100 flex-c">
        <div class="flexbox"></div>
        <div class="item_frame flexbox w-100 h-100 flex-c">
          <div class="w-100" v-bind:style="{ height: item_outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="h-100" v-bind:style="{ width: item_outbox_w + 'px' }"></div>
            <div id="itemcontent" class="flexbox w-100 h-100 flex-c" v-html="item(itemtype)" @click="initemgroup"></div>
            <div class="h-100" v-bind:style="{ width: item_outbox_w + 'px' }"></div>
          </div>

          <div class="w-100" v-bind:style="{ height: item_outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox"></div>
      </div>
      <div class="flexbox" style="flex: 2"></div>
    </div>
  </div>
</template>
<script>
import tools from "../lib/tools.js";

export default {
  name: "in_form",
  emits: ["info_close"],
  setup(_, { emit }) {
    function info_close(value) {
      emit("info_close", value);
    }

    return { info_close };
  },
  data() {
    return {
      outbox_h: 20,
      outbox_w: 0,
      scrollbox_h: 380,
      item_outbox_h: 20,
      item_outbox_w: 0,
      itemnum: 0, //如果是0，就是沒有通知
      itemtype: "achievement", //切換這個來修改css
      itembool: false, //子項目
      infobool: false, //如果有通知就打開"查看"按鈕
      clearmission: [],
      friendinvite:[],
      friendgift:[],
      friendindex:null,
    };
  },
  mounted() {

  },
  methods: {
    setwidth() {
      var group_h;
      var group_w;
      var group_bg_h;
      var group_bg_w;
      var group_outbox_h;
      var group_outbox_w;
      var scrollbox_h;
      var infoitem_h;
      var infoitem_w;
      var infoitem_bg_h;
      var infoitem_bg_w;
      var infoitem_outbox_h;
      var infoitem_outbox_w;

      if (window.innerWidth > 1920) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = window.innerHeight / 3;
        infoitem_w = window.innerWidth / 5;
      } else if (window.innerWidth >= 1440 && window.innerWidth < 1920) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 1024 && window.innerWidth < 1440) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 768 && window.innerWidth < 1024) {
        group_h = (window.innerHeight / 4.5) * 2.5;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      } else if (window.innerWidth >= 740 && window.innerWidth < 768) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      } else if (window.innerWidth >= 720 && window.innerWidth < 740) {
        group_h = (window.innerHeight / 4) * 2;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 654 && window.innerWidth < 720) {
        group_h = (window.innerHeight / 4) * 2;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 425 && window.innerWidth < 654) {
        group_h = (window.innerHeight / 4.5) * 2.5;
        group_w = (window.innerWidth / 3.5) * 1.5;

        infoitem_h = (window.innerHeight / 4.5) * 2.5;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      }

      group_bg_h = (group_w * 150) / 252;
      group_bg_w = (group_h * 252) / 150;
      scrollbox_h = (group_w * 85) / 252;
      group_outbox_w = (group_w - group_bg_w) / 2;
      group_outbox_h = (group_h - group_bg_h) / 2;
      // console.log(group_h, group_w, group_outbox_h, scrollbox_h);
      if (group_outbox_h < 0) {
        this.outbox_h = 0;
      } else {
        this.outbox_h = group_outbox_h;
      }
      if (group_outbox_w < 0) {
        this.outbox_w = 0;
      } else {
        this.outbox_w = group_outbox_w;
      }
      this.scrollbox_h = scrollbox_h;

      infoitem_bg_w = (infoitem_h * 178) / 150;
      infoitem_bg_h = (infoitem_w * 150) / 178;
      infoitem_outbox_w = (infoitem_w - infoitem_bg_w) / 2;
      infoitem_outbox_h = (infoitem_h - infoitem_bg_h) / 2;
      // console.log(
      //   window.innerWidth,
      //   infoitem_w,
      //   infoitem_bg_w,
      //   infoitem_outbox_w
      // );
      if (infoitem_outbox_h < 0) {
        this.item_outbox_h = 0;
      } else {
        this.item_outbox_h = infoitem_outbox_h;
      }
      if (infoitem_outbox_w < 0) {
        this.item_outbox_w = 0;
      } else {
        this.item_outbox_w = infoitem_outbox_w;
      }
    },
    refreshView() {
      //抓通知
      this.$http
        .get(tools.apiUrl + "players/" + tools.getCookie("id") + "/notices", {
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        })
        .then(
          (response) => {
            console.log("info", response.data);
            this.clearmission.length=0;
            this.friendinvite.length=0;
            this.friendgift.length=0;
            this.itemnum = response.data.length;
            for (var i = 0; i < response.data.length; i++) {
              switch (response.data[i].type) {
                case 'mission complete':
                  this.clearmission.push(response.data[i]);
                  break;
                case 'friend invitation':
                  this.friendinvite.push(response.data[i]);
                  break;
                case 'receive gift':
                  this.friendgift.push(response.data[i]);
                  break;
              }
            }
            if (this.itemnum == 0) {
              this.infobool = false;
              this.$parent.$refs.rightmenu.infodotbool = false;
            } else {
              this.infobool = true;
              this.$parent.$refs.rightmenu.infodotbool = true;
            }
            this.checkinfo();
          },
          (response) => {
            console.log(response);
          }
        );
    },
    checkinfo() {
      var innerhtml = "";

      if (this.itemnum == 0) {
        innerhtml = "您已查看完所有通知!";
        this.infobool = false;
      } else {
        for (var i = 0; i < this.clearmission.length; i++) {
          innerhtml += `
        <div class="heightSet w-100 d-flex">
        <input id="clearmission${i}" type="radio" name="inforadio" data-type="clearmission" value="${this.clearmission[i].content.mission.title}" />
          <label for="clearmission${i}">
            <span>完成「${this.clearmission[i].content.mission.title}」任務通知</span>
          </label>
        </div>
        `;
        }
        for(var j=0; j<this.friendinvite.length;j++){
        innerhtml += `
        <div class="heightSet w-100 d-flex">
        <input id="friendinvite${j}" type="radio" name="inforadio" data-type="friendinvite" data-index="${j}" value="${this.friendinvite[j].content.player.id}" />
          <label for="friendinvite${j}">
            <span>收到玩家「${this.friendinvite[j].content.player.account}」的好友邀請</span>
          </label>
        </div>
        `;
        }
        this.friendgift.forEach(function(element,index){
        innerhtml += `
        <div class="heightSet w-100 d-flex">
        <input id="friendgift${index}" type="radio" name="inforadio" data-type="friendgift" data-index="${index}" value="${element.content.player.id}" />
          <label for="friendgift${index}">
            <span>收到玩家「${element.content.player.account}」的好友贈禮</span>
          </label>
        </div>
        `;
        })
        this.infobool = true;
      }
      return innerhtml;
    },
    openitem() {
      var radioitem = document.getElementsByName("inforadio");
      //var selectedGender;
      for (var i = 0; i < radioitem.length; i++) {
        if (radioitem[i].checked) {
          console.log(radioitem[i].id);
          if(radioitem[i].dataset.type=="clearmission"){
          this.itemtype = radioitem[i].value;
          this.itembool = true;
          }else if(radioitem[i].dataset.type=="friendinvite"){
            this.itemtype="friendinvite";
            this.friendindex=radioitem[i].dataset.index;
            this.itembool=true;
          }else if(radioitem[i].dataset.type=="friendgift"){
            this.itemtype="friendgift";
            this.friendindex=radioitem[i].dataset.index;
            this.itembool=true;
          }
        }
      }
    },
    item(type) {
      var innerhtml = "";
      switch (type) {
        case "每日登入":
        case "累積登入":
        case"觀看網站中文章":
        case"觀看網站中影片":
        case"累積觀看網站中文章":
        case"累積觀看網站中影片":
        case"累積觀看線上展覽":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "誰是航運王":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game7pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "超級飛修員":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game8pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "無人商店採購":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game1pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "智慧製造洗手乳":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game2pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "彩繪技職樂園":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game3pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "智能雞舍":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game4pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "灑網抓魚感測":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game5pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "農業小幫手":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic game6pic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "花卉實驗室":
        case "建築師的一天":
        case "積木樂園":
        case "救援代號：台灣黑熊":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic giftpic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
          break;
        case "觀看線上展覽":
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pic showpic" style="flex:2;"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>完成「${type}」任務，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="info_check">確認</button>
        </div>
          `;
        break;
        case "friendinvite": //好友邀請
          innerhtml = `
        <div class="flexbox w-100 position-r">
          <div class="close_btn pointer" id="invite_close"></div>
        </div>
        <div class="informtitle flexbox w-100 h-100">
          交友邀請
        </div>
        <div class="flexbox w-100 h-100 flex-r" style="flex:3;">
          <div class="lionIcon flexbox w-100 h-100"></div>
          <div class="dataText lexbox h-100 flex-c">
            <p class="stage">${this.mailnewline(this.friendinvite[this.friendindex].content.player.account)}</p>
          </div>
        </div>
        <div class="widthSet flexbox w-100 h-100 flex-r">
          <div class="flexbox w-100 h-100">
            <button class="rejectBtn" id="invite_refuse">拒絕</button>
          </div>
          <div class="flexbox w-100 h-100">
            <button class="acceptBtn" id="invite_accept">接受</button>
          </div>
        </div>

        `;
          break;
        case "friendgift": //好友贈送
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 flex-c">
            <p>收到「${this.friendgift[this.friendindex].content.player.account}」的禮物，<br />請至禮物盒領取獎勵</p>
        </div>
        <div class="flexbox w-100 h-100">
            <button class="send_confirmBtn" id="send_check">確認</button>
        </div>
        `;
          break;
      }

      return innerhtml;
    },
    mailnewline(value){
      var newline=value.split('@');

      return newline[0]+"<br />@"+newline[1];
    },
    initemgroup(e) {
      console.log(e.target.id);
      if(e.target.id!=null||e.target.id!=""||e.target.id!=undefined){
      let clickedElId = e.target.id;
      switch (clickedElId) {
        case "info_check":
          var noticeid;
          for (var i = 0; i < this.clearmission.length; i++) {
            if (this.itemtype == this.clearmission[i].content.mission.title) {
              noticeid = this.clearmission[i].id;
            }
          }

          this.$http
            .patch(tools.apiUrl + "players/" + tools.getCookie("id") + "/notices/" + noticeid, { "player": tools.getCookie("id"), "notice": noticeid }, {
              emulateJSON: true,
              headers: { authorization: "Bearer " + tools.getCookie("token") },
            })
            .then(
              (response) => {
                this.refreshView();
              },
              (response) => {
                console.log(response);

              }
            );
          break;
        // case "invite_close"://關閉交友邀請

        //   break;
        case "invite_refuse"://拒絕交友邀請
          var renoticeid;
          for (var j = 0; j < this.friendinvite.length; j++) {
            if (this.friendindex == j) {
              renoticeid = this.friendinvite[j].id;
            }
          }
          this.$http
            .patch(tools.apiUrl + "players/" + tools.getCookie("id") + "/notices/" + renoticeid, { "player": tools.getCookie("id"), "notice": renoticeid }, {
              emulateJSON: true,
              headers: { authorization: "Bearer " + tools.getCookie("token") },
            })
            .then(
              (response) => {
                this.itemtype="";
                this.refreshView();
              },
              (response) => {
                console.log(response);

              }
            );
          break;
        case "invite_accept"://接受交友邀請
          var inviterid;
          var acnoticeid;
          for (var k = 0; k < this.friendinvite.length; k++) {
            if (this.friendindex == k) {
              acnoticeid = this.friendinvite[k].id;
              inviterid=this.friendinvite[k].content.player.id;

            }
          }

          this.$http
            .post(tools.apiUrl + "players/" + tools.getCookie("id") + "/confirmFriend/" + inviterid, { "notice_id": acnoticeid }, {
              emulateJSON: true,
              headers: { authorization: "Bearer " + tools.getCookie("token") },
            })
            .then(
              (response) => {
              this.refreshView();
              },
              (response) => {
                console.log(response);

              }
            );
          break;
        case "send_check"://確認好友贈送
          var senoticeid;
          senoticeid=this.friendgift[this.friendindex].id;

          this.$http
            .patch(tools.apiUrl + "players/" + tools.getCookie("id") + "/notices/" + senoticeid, { "player": tools.getCookie("id"), "notice": senoticeid }, {
              emulateJSON: true,
              headers: { authorization: "Bearer " + tools.getCookie("token") },
            })
            .then(
              (response) => {
                this.refreshView();
              },
              (response) => {
                console.log(response);

              }
            );
          break;
      }

      this.itembool = false;
      }


    },
  },
};
</script>

<style scoped>
#informpage {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 7;
}

#informgroup,
#informitem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

#informitem {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  z-index: 10;
}

.item_frame {
  background-image: url("../assets/use_obj_bg-01.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.d-flex {
  display: flex;
}

#itemcontent>>>.pointer,
.pointer {
  cursor: pointer;
}

#itemcontent>>>.w-100,
#infoitem>>>.w-100,
.w-100 {
  width: 100%;
}

#itemcontent>>>.h-100,
#infoitem>>>.h-100,
.h-100 {
  height: 100%;
}

#itemcontent>>>.flex-r,
.flex-r {
  flex-direction: row;
}

#itemcontent>>>.flex-c,
.flex-c {
  flex-direction: column;
}

#itemcontent>>>.position-r,
.position-r {
  position: relative;
}

#itemcontent>>>.flexbox,
.flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 1;
}

.parentbox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex: 1;
  height: 100%;
}

.infoFrame {
  background-image: url("../assets/inform_frame-01.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  flex: 2;
}

.back_btn {
  position: absolute;
  height: 10vh;
  width: 5vw;
  top: 0;
  left: -2.5vw;
  background-image: url("../assets/back_btn-01.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 5;
}

.close_btn {
  height: 10vh;
  width: 5vw;
  position: absolute;
  top: 0;
  right: -2.5vw;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../assets/close_btn-01.svg");
  z-index: 5;
  flex: 1;
}

#itemcontent>>>.close_btn {
  height: 10vh;
  width: 5vw;
  position: absolute;
  top: -5vh;
  right: -4.2vw;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../assets/close_btn-01.svg");
  z-index: 5;
  flex: 1;
}

.infoFrame>>> ::-webkit-scrollbar {
  color: rgb(255, 255, 255);
  font-size: 4.2vmin;
  font-weight: 600;
  text-align: center;
  width: 12px;
}

.infoFrame>>> ::-webkit-scrollbar-thumb {
  background: rgb(130, 191, 238);
}

.infoFrame>>> ::-webkit-scrollbar-track {
  background-color: rgb(50, 50, 50);
}

.infoFrame>>> ::-webkit-scrollbar-thumb {
  background: rgb(186, 222, 250);
  border-radius: 10px;
}

.infoFrame>>> ::-webkit-scrollbar-track {
  background-color: rgb(50, 50, 50);
  border: 2px solid rgb(255, 255, 255);
  border-radius: 10px;
}

button,
#itemcontent>>>button {
  border: none;
  background-color: rgba(0, 0, 0, 0);
  font-size: 3vmin;
  font-weight: 600;
  color: rgb(255, 255, 255);
}

.infoHeight {
  flex: 3;
}

.infoFrame>>>.block {
  overflow-x: hidden;
  overflow-y: auto;
  width: 95%;
}

#itemcontent >>> p{
  width: 80%;
  color: rgb(255, 255, 255);
  font-size: 2.8vmin;
  font-weight: 600;
}

#itemcontent>>>.pic {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

#itemcontent>>>.game1pic {
  background-image: url("../assets/missiongame/game_02_2.png");
}

#itemcontent>>>.game2pic {
  background-image: url("../assets/missiongame/game_03_2.png");
}

#itemcontent>>>.game3pic {
  background-image: url("../assets/missiongame/game_04_2.png");
}

#itemcontent>>>.game4pic {
  background-image: url("../assets/missiongame/game_05_2.png");
}

#itemcontent>>>.game5pic {
  background-image: url("../assets/missiongame/game_06_2.png");
}

#itemcontent>>>.game6pic {
  background-image: url("../assets/missiongame/game_07_2.png");
}

#itemcontent>>>.game7pic {
  background-image: url("../assets/missiongame/game_08_2.png");
}

#itemcontent>>>.game8pic {
  background-image: url("../assets/missiongame/game_09_2.png");
}

#itemcontent>>>.giftpic {
  background-image: url("../assets/giftbox-01.png");
}
#itemcontent>>>.showpic {
  background-image: url("../assets/missiongame/show.png");
}

@media screen and (min-width: 1920px) {

  /*通知視窗任務 */
  .infoFrame>>>#infoitem {
    position: relative;
    font-size: 3.5vmin;
    font-weight: 600;
    color: rgb(255, 255, 255);
  }

  .infoFrame>>>input[type="radio"] {
    display: none;
  }

  .infoFrame>>>input[type="radio"]+label {
    font-size: 3.2vmin;
    font-weight: normal;
    color: rgb(255, 255, 255);
  }

  .infoFrame>>>input[type="radio"]+label span {
    display: inline-block;
    background-color: rgba(121, 233, 255, 0.6);
    border-radius: 10px;
    cursor: pointer;
    width: 90%;
    height: 65px;
    position: relative;
    line-height: 65px;
    text-align: left;
    padding-left: 3%;
  }

  .infoFrame>>>input[type="radio"]:checked+label span {
    line-height: 61px;
    height: 61px;
    background-color: rgba(121, 233, 255, 0.6);
    border: 2px solid rgb(255, 255, 255);
  }

  .checkBtn {
    background-image: url("../assets/check_send_obj_btn-01.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    height: 70px;
  }

  .checkBtn button {
    font-size: 3.5vmin;
    cursor: pointer;
    width: 55%;
  }

  .infoFrame>>>.heightSet {
    height: 65px;
    margin: 0.5% 0;
    /* position: relative;
    top: 7vh; */
  }

  /*最新任務 */
