  }

  /*好友邀請 */
  #itemcontent>>>.informtitle {
    font-size: 3.8vmin;
    font-weight: 600;
    color: rgb(255, 255, 255);
    /* position: relative;
    top: -2vh; */
  }

  #itemcontent>>>.lionIcon {
    background-image: url("../assets/lionIcon-01.png");
    background-size: contain;
    background-repeat: no-repeat;
