  }

  #itemcontent>>>.dataText p {
    margin-top: 5px;
    margin-bottom: 5px;
  }

  #itemcontent>>>.stage {
    font-size: 2.2vmin;
    font-weight: 600;
    color: rgb(255, 255, 255);
    letter-spacing: 2px;
  }

  #itemcontent>>>.rejectBtn {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../assets/reject_btn-01.svg");
    cursor: pointer;
    width: 100%;
    height: 45px;
    font-size: 2.5vmin;
    position: relative;
    top: 50%;
