    font-size: 3.5vmin;
    font-weight: 600;
    color: rgb(255, 255, 255);
    height: 30px;
  }

  #itemcontent>>>.send_confirmBtn {
    font-size: 3.5vmin;
    font-weight: 600;
    color: rgb(255, 255, 255);
    width: 50%;
    height: 50px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../assets/confirm_btn-01.svg");
    position: relative;
    top: 3vh;
    cursor: pointer;
  }

  /*達成新成就 */
