<template>
  <div class="maskpage">
    <div id="mask_choose" class="d-flex flex-c">
      <div class="flexbox w-100 h-100 position-r" style="flex: 1.5">
        <div class="close_btn pointer" @click="mask_close(false)"></div>
      </div>
      <div class="flexbox flex-r w-100 h-100" style="flex: 1.75">
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 pointer" style="flex: 2">
          <input v-bind:id="male + 'childradio'" type="radio" name="maskradio" :class="male + 'child'"
            :value="male + 'child'" checked />
          <label :for="male + 'childradio'" class="h-100 flexbox">
            <span class="position-r">
              <p class="growStage">幼年獅</p>
            </span>
          </label>
        </div>
        <div class="flexbox w-100 h-100 pointer" style="flex: 2">
          <input v-bind:id="male + 'adultradio'" type="radio" name="maskradio" :class="male + 'adult'"
            :value="male + 'adult'" />
          <label :for="male + 'adultradio'" class="h-100 flexbox">
            <span class="position-r">
              <p class="growStage">成年獅</p>
            </span>
          </label>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100">
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100">
          <button class="colorBtn" @click="startdraw()">上色</button>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
    </div>
    <div id="maskgroup" class="d-flex flex-r" v-show="maskdrawbool">
      <div class="flexbox w-100 h-100" style="flex: 0.5"></div>
      <!-- 左邊欄位 -->
      <div class="flexbox flex-c w-100 h-100">
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 lefttitle"></div>
        <!-- 填色區 -->
        <div class="flexbox w-100 h-100 colorplate flexSetting">
          <div class="d-flex pointer" v-for="(color, index) in drawarr" :key="color.name" @click="changeColor(index)">
            <input v-bind:id="color.name + 'radio'" type="radio" name="drawradio" :class="color.name + 'colorplate'"
              :value="color.name" :checked="index == 0" />
            <label :for="color.name + 'radio'" class="h-100 flexbox">
              <span class="pointer"> </span>
            </label>
          </div>
        </div>
        <!-- 上/下一步 -->
        <div class="flexbox w-100 h-100">
          <div class="flexbox w-100 maskbackbtn pointer" @click="goTo(-1)"></div>
          <div class="flexbox w-100 masknextbtn pointer" @click="goTo(1)"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <!-- 中間欄位 -->
      <div class="flexbox flex-c w-100 h-100" style="flex: 2">
        <div class="titleFont flexbox w-100 h-100">點擊想要上色的區域</div>
        <!-- 上色區域 -->
        <canvas id="mask_canvas" @mousedown="onCanvasMouseDown" @mouseup="onCanvasMouseUp"
          @mousemove="onCanvasMouseMove" @touchstart="onCanvasMouseDown" @touchmove="onCanvasMouseMove"
          @touchend="onCanvasMouseUp"></canvas>
        <!-- 按鈕 -->
        <div class="flexbox w-100 h-100" style="flex: 0.5">
          <div class="flexbox w-100 h-100">
            <button class="cancleOperateBtn pointer" @click="canceldraw()">
              取消操作
            </button>
          </div>
          <div class="flexbox w-100 h-100">
            <button class="colorBtn pointer" @click="savedraw()">確認儲存</button>
          </div>
        </div>
        <div class="flexbox w-100 h-100" style="flex: 0.5"></div>
      </div>
      <!-- 右邊欄位 -->
      <div class="flexbox flex-c w-100 h-100">
        <div class="flexbox w-100 h-100" style="flex: 0.5"></div>
        <!-- 印章 -->
        <div class="stampgroup flexbox w-100 h-100 flex-c">
          <div class="titleFont flexbox w-100 h-100">印章</div>
          <div class="flexbox w-100 pointer" v-for="(stamp, index) in stamparr" :key="stamp">
            <input v-bind:id="stamp + 'radio'" type="radio" name="stampradio" :class="stamp" :value="stamp" />
            <label :for="stamp + 'radio'" class="h-100 flexbox">
              <span class="pointer" @click="changeStamp(index)"> </span>
            </label>
          </div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100" style="flex: 0.5"></div>
    </div>
  </div>
</template>
<script>
import tools from "../lib/tools.js";

export default {
  name: "mask_page",
  emits: ["mask_close"],
  setup(_, { emit }) {
    function mask_close(value) {
      emit("mask_close", value);
    }
    return { mask_close };
  },
  data() {
    return {
      male: "boy",
      maletype: "boychild",
      maskid: 0,
      maskdrawbool: false,
      drawarr: [{
        name: "red",
        color: "#c30d23"
      },
      {
        name: "malelion",
        color: "#edd87b"
      },
      {
        name: "orange",
        color: "#ea5513"
      },
      {
        name: "femalelion",
        color: "#f0d296"
      },
      {
        name: "yellow",
        color: "#faef01"
      },
      {
        name: "brown",
        color: "#c9793c"
      },
      {
        name: "green",
        color: "#016934"
      },
      {
        name: "deepbrown",
        color: "#8b631e"
      },
      {
        name: "blue",
        color: "#046eb8"
      },
      {
        name: "black",
        color: "#231816"
      },
      {
        name: "purple",
        color: "#611987"
      },
      {
        name: "white",
        color: "#ffffff"
      }
      ],
      canvas: null,
      ctx: null,
      stampIndex: 1,
      isDrawing: false,
      isStamp: false,
      stamparr: ["heart", "star", "flower"],
      canvasheightarr: [500, 350, 250, 227],
      canvaswidtharr: [534, 376, 267, 243],
    };
  },
  mounted() {
    //init canavas

    this.canvas = document.getElementById('mask_canvas');
    if (window.innerWidth >= 1440) {
      this.canvas.width = 534;
      this.canvas.height = 500;
    } else if (window.innerWidth < 1440 && window.innerWidth >= 1024) {
      this.canvas.width = 376;
      this.canvas.height = 350;
    } else if (window.innerWidth < 1024 && window.innerWidth >= 768) {
      this.canvas.width = 267;
      this.canvas.height = 250;
    } else {
      this.canvas.width = 243;
      this.canvas.height = 227;
    }

    this.ctx = this.canvas.getContext('2d');
    var state = this.ctx.getImageData(0, 0, this.canvas.width, this.canvas.height);

    window.history.pushState(state, null);
    window.addEventListener('popstate', changeStep, false);

    function changeStep(e) {
      // 清除畫布
      var canvas = document.getElementById('mask_canvas');
      var ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      if (e.state) {
        ctx.putImageData(e.state, 0, 0);
      }
    }
    this.ctx.strokeStyle = this.drawarr[0].color;
    this.isDrawing = false;
    document.onselectstart = function () {
      return false
    };




  },
  methods: {
    goTo(pos) {
      window.history.go(pos);
    },
    getMousePos(canvas, evt) {
      var rect = this.canvas.getBoundingClientRect();
      if (typeof evt.targetTouches!= 'undefined') {
        return {
          x: evt.targetTouches[0].pageX - rect.left,
          y: evt.targetTouches[0].pageY - rect.top
        };
      } else {
        return {
          x: evt.clientX - rect.left,
          y: evt.clientY - rect.top
        };
      }

    },
    onCanvasMouseDown(e) {
      var currentPosition = this.getMousePos(this.canvas, e);

      if (this.isStamp) {
        var img = new Image();
        let vm = this;
        img.src = require("@/assets/draw/stamp_no_" + this.stampIndex + ".svg"); //設定圖片路徑
        //console.log(img.src,this.stampIndex);
        img.onload = function () {
          vm.ctx.drawImage(img, currentPosition.x - 25, currentPosition.y - 25, 50, 50)
        }

        return;
      }
      this.isDrawing = true;
      this.ctx.beginPath();
      this.ctx.moveTo(currentPosition.x, currentPosition.y)
      this.ctx.lineWidth = 10;
      this.ctx.lineCap = "round";
    },
    onCanvasMouseUp(e) {
      this.isDrawing = false;
      var state = this.ctx.getImageData(0, 0, this.canvas.width, this.canvas.height);
      window.history.pushState(state, null);
    },
    onCanvasMouseMove(e) {
      if (this.isDrawing) {
        var currentPosition = this.getMousePos(this.canvas, e);
        console.log(currentPosition);
        this.ctx.lineTo(currentPosition.x, currentPosition.y)
        this.ctx.stroke();
      }
    }, changeColor(index) {
      this.isStamp = false;
      this.ctx.strokeStyle = this.drawarr[index].color;
    }, changeStamp(index) {
      this.isStamp = true;
      this.stampIndex = index + 1;
      console.log('stamp');
    },
    startdraw() {
      var radioitem = document.getElementsByName("maskradio");

      for (var i = 0; i < radioitem.length; i++) {
        if (radioitem[i].checked) {
          this.maletype = radioitem[i].value;
          var img = new Image();
          var vm = this;
          switch (this.maletype) {
            case 'boychild':
              img.src = require("@/assets/draw/child_male_lion_nocolor-01.svg");
              break;
            case 'boyadult':
              img.src = require("@/assets/draw/adult_male_lion_nocolor-01.svg");
              break;
            case 'girlchild':
              img.src = require("@/assets/draw/child_female_lion_nocolor-01.svg");
              break;
            case 'girladult':
              img.src = require("@/assets/draw/adult_female_lion_nocolor-01.svg");
              break;
          }
          img.onload = function () {
            vm.ctx.drawImage(img, 0, 0, vm.canvas.width, vm.canvas.height);
            console.log(img.src);
          }
        }
      }

      this.maskdrawbool = true;
    },
    canceldraw() {
      this.maskdrawbool = false;
      // 清除畫布

      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    },
    savedraw() {
      var lionname = this.$parent.name;
      var myDate = new Date();
      var year = myDate.getFullYear();
      var month = myDate.getMonth() + 1;
      var day = myDate.getDate();
      var hrs = myDate.getHours();
      var mins = myDate.getMinutes();
      var secs = myDate.getSeconds();

      var handimg = new Image();
      // var drawimg = new Image();
      var vm = this;
      var savecanvas = document.createElement('canvas');
      var savectx = savecanvas.getContext('2d');
      savecanvas.id = "savecanvas";
      var resizedcanvas = document.createElement('canvas');
      var resizedctx = resizedcanvas.getContext('2d');
      resizedcanvas.width = 823;
      resizedcanvas.height = 601;
      resizedctx.drawImage(vm.canvas, 322, 53, 249, 233);

      switch (this.male) {
        case 'boy':
          handimg.src = require("@/assets/boy_hand-01.svg");
          break;
        case 'girl':
          handimg.src = require("@/assets/girl_hand-01.svg");
          break;
      }
      console.log('saveclick')

      handimg.onload = function () {
        savecanvas.width = 823;
        savecanvas.height = 601;

        savectx.drawImage(handimg, 0, 0, savecanvas.width, savecanvas.height);
        savectx.drawImage(resizedcanvas, 0, 0, savecanvas.width, savecanvas.height);
        vm.$http
          .post(tools.apiUrl + "backpack/" + vm.maskid + "/mask", { "image": savecanvas.toDataURL("image/png") }, {
            headers: { authorization: "Bearer " + tools.getCookie("token") },
          })
          .then(
            (response) => {
              console.log(response);
              vm.maskdrawbool = false;
              vm.$parent.maskbool = false;
            },
            (response) => {
              console.log(response);
            }
          );
        // let link = document.createElement('a');
        // link.href = savecanvas.toDataURL("image/png");
        // link.setAttribute('id', 'maskimglink');
        // link.setAttribute('download', `mask_${lionname}_${year}${month}${day}_${hrs}_${mins}_${secs}.png`);
        // link.style.display = 'none';
        // document.body.appendChild(link);
        // link.click();
        // document.getElementById('maskimglink').remove();

      }



    },

  },
};
</script>

