<template>
  <!-- 測驗 -->
  <div id="question_page">
    <div class="flexbox"></div>
    <div class="flexbox centerbox">
      <div class="flexbox"></div>
      <div
        v-html="questiontopic(questionnum)"
        class="qs_frame box"
        id="questiondiv"
        @click="point"
      ></div>
      <div class="flexbox"></div>
    </div>
    <div class="flexbox"></div>
  </div>
</template>

<script>
var chooserarray = [];

import json from "@/assets/json/investigation.json";
import tools from "../lib/tools.js";

export default {
  name: "question_page",
  emits: ["question_backbtn"],
  setup(_, { emit }) {
    function question_backbtn(value) {
      emit("question_backbtn", value);
    }

    return { question_backbtn };
  },
  data() {
    return {
      jsongroup: json,
      questiongroup: [],
      answergroup: [],
      fishernum: 0,
      farmernum: 0,
      engineernum: 0,
      questionnum: null,
      outbox_h: 80,
    };
  },
  created() {
    window.addEventListener("resize", this.setwidth);
  },
  unmounted() {
    window.removeEventListener("resize", this.setwidth);
  },
  mounted() {
    this.$http
      .get(tools.apiUrl + "questions/" + tools.getCookie("id"), {
        headers: { authorization: "Bearer " + tools.getCookie("token") },
      })
      .then((response) => {
        console.log(response.data);
        this.questiongroup = response.data;
        this.questionnum = 0;
      });
    this.setwidth();
    window.addEventListener("resize", this.setwidth);
  },
  methods: {
    // myEventHandler(e){

    // },
    getquestionfun() {
      this.$http
        .get(tools.apiUrl + "questions/" + tools.getCookie("id"), {
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        })
        .then((response) => {
          console.log(response.data);
          this.questiongroup = response.data;
          this.questionnum = 0;
        });
    },
    setwidth() {
      var qs_frame_h = (window.innerHeight / 6) * 4; //
      var qs_frame_w = (window.innerWidth / 4) * 2;
      var qs_frame_bg_w = qs_frame_w;
      var qs_frame_bg_h = (qs_frame_bg_w * 150) / 252;
      var qs_frame_bg_outbox_h = (qs_frame_h - qs_frame_bg_h) / 2;
      console.log(
        qs_frame_h,
        qs_frame_bg_w,
        qs_frame_bg_h,
        qs_frame_bg_outbox_h
      );
      this.outbox_h = qs_frame_bg_outbox_h;
    },
    checktypegroup(scores) {
      var engineer = this.checktype(scores.engineer);
      var farmer = this.checktype(scores.farmer);
      var fisher = this.checktype(scores.fisher);
      var type = "";
      if (engineer) {
        type = "engineer";
      } else if (farmer) {
        type = "farmer";
      } else if (fisher) {
        type = "fisher";
      }
      return type;
    },
    checktype(item) {
      for (var i = 0; i < item.length; i++) {
        if (item[i] == 1) {
          return true;
        }
      }
      return false;
    },
    checkanswergroup(scores) {
      var engineer = this.checkanswer(scores.engineer);
      var farmer = this.checkanswer(scores.farmer);
      var fisher = this.checkanswer(scores.fisher);
      var answer;
      if (engineer != 0) {
        answer = engineer;
      } else if (farmer != 0) {
        answer = farmer;
      } else if (fisher != 0) {
        answer = fisher;
      }

      return answer;
    },
    checkanswer(item) {
      for (var i = 0; i < item.length; i++) {
        if (item[i] == 1) {
          return i;
        }
      }
      return 0;
    },
    questiontopic(value) {
      if (this.questiongroup.length != 0) {
        //改變題目
        var inner = "";
        var choice = this.questiongroup[value].choice;
        var length = this.questiongroup[value].choice.length;
        var type = this.checktypegroup(this.questiongroup[value].scores);
        var answer = this.checkanswergroup(this.questiongroup[value].scores);
        var option1 = "";
        var option2 = "";
        var option3 = "";

        for (var i = 0; i < length; i++) {
          switch (i) {
            case 0:
              if (i == answer) {
                option1 = `<div class="answer1" id="answer1" data-type="${type}">A ${choice[i]}</div>`;
              } else {
                option1 = `<div class="answer1" id="answer1" data-type="0">A ${choice[i]}</div>`;
              }

              break;
            case 1:
              if (i == answer) {
                option2 = `<div class="answer2" id="answer2" data-type="${type}">B ${choice[i]}</div>`;
              } else {
                option2 = `<div class="answer2" id="answer2" data-type="0">B ${choice[i]}</div>`;
              }
              break;
            case 2:
              if (i == answer) {
                option3 = `<div class="answer3" id="answer3" data-type="${type}">C ${choice[i]}</div>`;
              } else {
                option3 = `<div class="answer3" id="answer3" data-type="0">C ${choice[i]}</div>`;
              }
              break;
          }
        }
        inner = `
      <div style="height:${this.outbox_h}px"></div>
      <div class="flexbox" style="position:relative;">
        <div class="back_btn" id="q_back_btn"></div>
        <div class="close_btn" id="q_close_btn"></div>
      </div>
      <div class="qs_group">
      <div class="flexbox" style="height:100%;"></div>
        <div class="flexbox5">
          <div class="context3 flexbox">在養成梅克獅之前，飼育員有幾個問題想要問你:</div>
          <div class="context2 flexbox">Q${value + 1} ${
          this.questiongroup[value].content
        }</div>
          <div class="answers flexbox">
              ${option1 + option2 + option3}
          </div>
        </div>
        <div class="flexbox" style="height:100%;"></div>
      </div>
      <div style="height:${this.outbox_h}px"></div>
    `;
        // console.log(option1+option2+option3,inner);
        return inner;
      }
    },
    point(e) {
      //計算類型得分
      // console.log(e);
      let clickedElId = e.target.id;
      // console.log(clickedElId);
      if (
        clickedElId == "answer1" ||
        clickedElId == "answer2" ||
        clickedElId == "answer3"
      ) {
        let datatype = document.getElementById(clickedElId).dataset.type;
        if (datatype !== undefined || datatype !== null) {
          this.answergroup.push(datatype);
          switch (datatype) {
            case "fisher":
              this.fishernum++;
              break;
            case "farmer":
              this.farmernum++;
              break;
            case "engineer":
              this.engineernum++;
              break;
          }
          if (this.questionnum == 14) {
            //當測驗做完之後
            console.log("question over", this.answergroup);
            var max = 0;
            var overtype = "";
            if (this.fishernum >= this.farmernum) {
              max = this.fishernum;
              overtype = "fisher";
            } else {
              max = this.farmernum;
              overtype = "farmer";
            }
            if (max < this.engineernum) {
              max = this.engineernum;
              overtype = "engineer";
            }
            // console.log(overtype);
            //確保重新產生亂數題庫
            this.fishernum = 0;
            this.farmernum = 0;
            this.engineernum = 0;
            this.answergroup.length = 0;
            this.questiongroup.length = 0;

            console.log(
              "fishernum:",
              this.fishernum,
              "farmernum:",
              this.farmernum,
              "engineernum:",
              this.engineernum,
              "answergroup:",
              this.answergroup,
              "questiongroup:",
              this.questiongroup
            );
            this.$parent.tointroducefun("introduce", overtype);
          } else {
            this.questionnum++;
            console.log(
              "fisher:" +
                this.fishernum +
                ",farmer:" +
                this.farmernum +
                ",engineer:" +
                this.engineernum
            );
          }
        }
      } else if (clickedElId == "q_back_btn") {
        this.previousfun();
      } else if (clickedElId == "q_close_btn") {
        this.closebtnfun();
      }
    },
    previousfun() {
      //回上一步
      if (this.questionnum == 0) {
        this.$parent.changefun("getlion");
        this.questiongroup.length = 0;
      } else {
        var temporaryarr = [];
        this.questionnum--;
        // console.log(this.answergroup,this.questionnum);
        switch (this.answergroup[this.questionnum]) {
          case "fisher":
            this.fishernum--;
            break;
          case "farmer":
            this.farmernum--;
            break;
          case "engineer":
            this.engineernum--;
            break;
        }

        for (var i = 0; i < this.questionnum; i++) {
          temporaryarr[i] = this.answergroup[i];
        }
        this.answergroup = temporaryarr;

        console.log(this.answergroup, this.questionnum);
        console.log(
          "fisher:" +
            this.fishernum +
            ",farmer:" +
            this.farmernum +
            ",engineer:" +
            this.engineernum
        );
      }
    },
    closebtnfun() {
      this.questiongroup.length = 0;
      this.$parent.changefun("getlion");
    },
  },
};
</script>

