              "rookie",
              "craftsman",
              "shokunin",
              "master",
            ];
            var occupationarr = ["farmer", "fisher", "engineer"];
            this.bookitemarr.length = 0;
            this.farmeritemarr.length = 0;
            this.fisheritemarr.length = 0;
            this.engineeritemarr.length = 0;
            for (var i = 0; i < occupationarr.length; i++) {
              for (var j = 0; j < classarr.length; j++) {
                switch (occupationarr[i]) {
                  case "farmer":
                    this.farmeritemarr.push({
                      class: classarr[j],
                      get: false,
                      occupation: occupationarr[i],
                    });
                    break;
                  case "fisher":
                    this.fisheritemarr.push({
                      class: classarr[j],
                      get: false,
                      occupation: occupationarr[i],
                    });
                    break;
                  case "engineer":
                    this.engineeritemarr.push({
                      class: classarr[j],
                      get: false,
