    },
    setwidth() {
      var level_tag_h = (window.innerHeight / 8) * 2;
      var level_bg_h = (level_tag_h / 6) * 4;
      var level_tag_w = (window.innerWidth * 0.9) / 3;
      var level_bg_w = (level_bg_h * 300) / 114;
      var level_bg_outbox_w = (level_tag_w - level_bg_w) / 2;
      console.log(level_bg_h, level_bg_w, level_bg_outbox_w);

      if (level_bg_outbox_w < 0) {
        this.outbox_w = { width: 0 };
      } else {
        this.outbox_w = { width: level_bg_outbox_w + "px" };
      }

      var group_h;
      var group_w;
      var group_bg_h;
      var group_bg_w;
      var group_outbox_h;
      var group_outbox_w;
      var biggroup_h;
      var biggroup_w;
      var biggroup_bg_h;
      var biggroup_bg_w;
      var biggroup_outbox_h;
      var biggroup_outbox_w;

      var infoitem_h;
      var infoitem_w;
      var infoitem_bg_h;
      var infoitem_bg_w;
      var infoitem_outbox_h;
      var infoitem_outbox_w;

      group_h = (window.innerHeight / 5) * 3;
      group_w = (window.innerWidth / 4) * 2;
      group_bg_h = (group_w * 150) / 252;
      group_bg_w = (group_h * 252) / 150;
      group_outbox_w = (group_w - group_bg_w) / 2;
      group_outbox_h = (group_h - group_bg_h) / 2;
      if (window.innerWidth > 1920) {
        biggroup_h = (window.innerHeight / 9) * 7;
        biggroup_w = (window.innerWidth / 6) * 4;
      } else if (window.innerWidth >= 1440 && window.innerWidth < 1920) {
        biggroup_h = (window.innerHeight / 9) * 7;
        biggroup_w = (window.innerWidth / 7) * 5;
      } else if (window.innerWidth >= 1024 && window.innerWidth < 1440) {
        biggroup_h = (window.innerHeight / 9) * 7;
        biggroup_w = (window.innerWidth / 7) * 5;
      } else if (window.innerWidth >= 768 && window.innerWidth < 1024) {
        biggroup_h = (window.innerHeight / 8) * 6;
        biggroup_w = (window.innerWidth / 5) * 3;
      } else if (window.innerWidth >= 740 && window.innerWidth < 768) {
        biggroup_h = (window.innerHeight / 7) * 5;
        biggroup_w = (window.innerWidth / 5) * 3;
      } else if (window.innerWidth >= 720 && window.innerWidth < 740) {
        biggroup_h = (window.innerHeight / 9) * 7;
        biggroup_w = (window.innerWidth / 7) * 5;
      } else if (window.innerWidth >= 654 && window.innerWidth < 720) {
        biggroup_h = (window.innerHeight / 7.5) * 5.5;
        biggroup_w = (window.innerWidth / 7) * 5;
      } else if (window.innerWidth >= 425 && window.innerWidth < 654) {
        biggroup_h = (window.innerHeight / 10) * 8;
        biggroup_w = (window.innerWidth / 5) * 3;
      }

      biggroup_bg_h = (biggroup_w * 150) / 262;
      biggroup_bg_w = (biggroup_h * 262) / 150;
      biggroup_outbox_w = (biggroup_w - biggroup_bg_w) / 2;
      biggroup_outbox_h = (biggroup_h - biggroup_bg_h) / 2;

      infoitem_h = window.innerHeight / 3;
      infoitem_w = window.innerWidth / 5;
      infoitem_bg_w = (infoitem_h * 178) / 150;
      infoitem_bg_h = (infoitem_w * 150) / 178;
      infoitem_outbox_w = (infoitem_w - infoitem_bg_w) / 2;
      infoitem_outbox_h = (infoitem_h - infoitem_bg_h) / 2;

      if (group_outbox_h < 0) {
        this.top_outbox_h = 0;
      } else {
        this.top_outbox_h = group_outbox_h;
      }
      if (group_outbox_w < 0) {
        this.top_outbox_w = 0;
      } else {
        this.top_outbox_w = group_outbox_w;
      }
      if (biggroup_outbox_h < 0) {
        this.big_outbox_h = 0;
      } else {
        this.big_outbox_h = biggroup_outbox_h;
      }
      if (biggroup_outbox_w < 0) {
        this.big_outbox_w = 0;
      } else {
        this.big_outbox_w = biggroup_outbox_w;
      }

      if (infoitem_outbox_h < 0) {
        this.item_outbox_h = 0;
      } else {
        this.item_outbox_h = infoitem_outbox_h;
      }
      if (infoitem_outbox_w < 0) {
        this.item_outbox_w = 0;
      } else {
        this.item_outbox_w = infoitem_outbox_w;
      }
    },
    setpropsclass(value) {
      var propsclass = "";

      // console.log(this.propscss);
      for (var i = 0; i < this.propscss.length; i++) {
        if (value == this.propscss[i].name) {
          propsclass = this.propscss[i].cssname;
        }
      }

      return propsclass;
    },
    changebackpagefun(value) {
      this.boxradiotype = value;
    },
    chagneclothgroupfun(value) {
      this.clothgroupradiotype = value;
    },
    boxclick(value) {
      tools.audioplay();
      this.refreshView();
      switch (value) {
        case "backpage":
          var backpageradio = document.getElementsByName("boxradio");
          for (var g = 0; g < backpageradio.length; g++) {
            backpageradio[g].checked = false;
          }
          backpageradio[0].checked = true;
          this.boxradiotype = "props";
          this.backpagebool = true;
          this.clothbool = false;
          this.missionbool = false;

          break;
        case "clothgroup":
          var clothgroupradio = document.getElementsByName("clothgroupradio");
          for (var j = 0; j < clothgroupradio.length; j++) {
            clothgroupradio[j].checked = false;
          }
          clothgroupradio[0].checked = true;
          this.clothgroupradiotype = "cloth";
          this.backpagebool = false;
          this.clothbool = true;
          this.missionbool = false;
          break;
        case "mission":
          var missionradio = document.getElementsByName("missionradio");
          for (var k = 0; k < missionradio.length; k++) {
            missionradio[k].checked = false;
          }
          this.backpagebool = false;
          this.clothbool = false;
          this.missionbool = true;
          this.reddotbool = this.checkoncemission();
          break;
      }
      this.boxbool = true;
      this.lionflex = 3;
    },
    checkoncemission() {
      for (var i = 0; i < this.missionarr.length; i++) {
        if (this.missionarr[i].type == "once") {
          return true;
        }
      }
      return false;
    },
    closebox(e) {
      tools.audioplay();
      var clickedElClassList = e.target.classList;

      if (this.checkclick(clickedElClassList)) {
        this.boxbool = false;
        this.lionflex = 5;
        console.log("!click");
      }
    },
    checkclick(value) {
      tools.audioplay();
      var list = "unclosebox";
      for (var j = 0; j < value.length; j++) {
        if (value[j] == list) {
          return false;
        }
      }
      return true;
    },
    clickmission(value) {
      tools.audioplay();
      switch (value.title) {
        case "每日登入":
          var myDate = new Date();
          var hrs = myDate.getHours();

          var greet;

          if (hrs < 11) greet = "早安";
          else if (hrs >= 11 && hrs <= 17) greet = "午安";
          else if (hrs >= 17 && hrs <= 23) greet = "晚安";
          this.bigmission = "mission";
          this.missionshow = true;
          if (hrs < 7 || hrs > 23) {
            this.isShowPic = false;
            this.top_content = "獅獅睡著了，<br>早上7點再來看牠吧！";
          } else {
            this.isShowPic = true;
            this.pic = "chicken";
            this.top_content =
              greet + "!<br>歡迎來到養成梅克獅，<br>恭喜你獲得一塊雞肉";
          }
          break;
        case "累積登入":
          this.top_content =
            value.content + "<br />目前進度" + value.state + "/7(天)";
          this.bigmission = "mission";
          this.missionshow = true;
          this.isShowPic = false;
          break;
        case "累積觀看網站中文章":
          this.top_content =
            value.content + "<br />目前進度" + value.state + "/5(次)";
          this.bigmission = "writing";
          this.missionshow = true;
          this.isShowPic = false;
          break;
        case "累積觀看網站中影片":
          this.top_content =
            value.content + "<br />目前進度" + value.state + "/5(次)";
          this.bigmission = "video";
          this.missionshow = true;
          this.isShowPic = false;
          break;
        case "累積觀看線上展覽":
          this.top_content =
            value.content + "<br />目前進度" + value.state + "/5(個)";
          this.bigmission = "showmission";
          this.missionshow = true;
          this.isShowPic = false;
          break;
        case "誰是航運王":
          this.top_content = value.content;
          this.top_gameurl = value.data.url;
          this.pic = "game7";
          this.gamemissionshow = true;
          break;
        case "超級飛修員":
          this.top_content = value.content;
          this.top_gameurl = value.data.url;
          this.pic = "game8";
          this.gamemissionshow = true;
          break;
        case "無人商店採購":
          this.top_content = value.content;
