    top_feedcheckbtn() {
      this.feedfoodnum = document.getElementById("sendnum").value;
      this.fooditemshowbool = false;
      this.feeditemshowbool = true;
      var feedid = 0;
      var postdata;
      switch (this.pic) {
        case "beef":
          this.top_content = `餵食牛肉X${this.feedfoodnum}<br />經驗值+${this.feedfoodnum * 30
            }`;
          break;
        case "chicken":
          this.top_content = `餵食雞肉X${this.feedfoodnum}<br />經驗值+${this.feedfoodnum * 10
            }`;
          feedid = 1;
          break;
        case "pig":
          this.top_content = `餵食豬肉X${this.feedfoodnum}<br />經驗值+${this.feedfoodnum * 20
            }`;
          break;
      }
      postdata = {
        backpack_id: this.fooditem.id,
        quantity: parseInt(this.feedfoodnum),
      };
      this.$http
        .patch(tools.apiUrl + "lions/" + this.lion.id + "/feed", postdata, {
          emulateJSON: true,
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        })
        .then(
          (response) => {
            this.refreshView();
          },
          (response) => {
            console.log(response);
            this.$parent.alert("網路連線問題，請再試一次!");
          }
        );
      document.getElementById("sendnum").value = 1;
    },
    levelupfun() {
      if (this.level < 10) {
        this.progress = this.exp * 2;

      }
      if (this.level > 10 && this.level < 20) {
        this.progress = this.exp;

      }
      if (this.level > 20 && this.level < 30) {
        this.progress = (this.exp / 150) * 100;

      }
      if (this.level > 30 && this.level < 40) {
        this.progress = this.exp / 2;

      }
      if (this.level > 40 && this.level < 50) {
        this.progress = (this.exp / 250) * 100;

      }
      if (this.level > 50 && this.level < 60) {
        this.progress = this.exp / 3;

      }
      if (this.level >= 60) {
        this.progress = 100;

      }

      // var postdata
      //把增加經驗值跟等級回傳回去
    },
