        this.top_content != "兌換券"
      ) {
        alert("前往衣櫃→配件穿戴");
      } else if (
        this.top_contentc_btn == "使用" &&
        this.top_content == "臉譜(未繪製)"
      ) {
        this.clickmaskbtn(true);
        this.topscenebool = false;
        // alert("尚未開放");
      } else if (
        this.top_contentc_btn == "使用" &&
        this.top_content == "兌換券"
      ) {
        alert("尚未開放");
      } else {
        this.topscenebool = false;
        this.missionshow = false;
        this.badgebool = false;
        this.foodbool = false;
        this.gamemissionshow = false;
        this.bookbool = false;
        this.specialbool = false;
        this.showmissiondatabool = false;
        this.bigmissionshow = false;
        this.showmissionbool = false;
        this.top_contentc_btn = "確認";
        this.top_contentg_btn = "贈送";
      }
    },
    top_closebtn() {
      this.topscenebool = false;
      this.missionshow = false;
      this.badgebool = false;
      this.foodbool = false;
      this.gamemissionshow = false;
      this.bookbool = false;
      this.specialbool = false;
      this.showmissiondatabool = false;
      this.bigmissionshow = false;
      this.showmissionbool = false;
      this.qrcodebool = false;
      this.top_contentc_btn = "確認";
      this.top_contentg_btn = "贈送";
      clearInterval(this.checkwriting);
      clearInterval(this.checkvideo);
      this.bigmissioncontent = "";
    },
    top_itemscene() {
      switch (this.top_contentg_btn) {
        case "贈送":
          if (
            (this.top_contentc_btn == "使用" &&
              this.top_content == "臉譜(未繪製)" || this.top_contentc_btn == "使用" &&
              this.top_content == "臉譜(已繪製)")
          ) {
            alert("此道具不可贈送!");
          } else {
            alert("請至好友列表贈送");
          }

          break;
        case "更換":
          //脫 之後穿

          var offid = null;
          console.log(this.nchangeClothes);
          if (
            Object.prototype.hasOwnProperty.call(this.nchangeClothes, "cloth")
          ) {
            switch (this.nchangeClothes.cloth.type) {
              case "cloth":
                if (
                  Object.prototype.hasOwnProperty.call(this.clothes, "cloth")
                ) {
                  offid = this.clothes.cloth.id;
                }

                break;
              case "pants":
                if (
                  Object.prototype.hasOwnProperty.call(this.clothes, "pants")
                ) {
                  offid = this.clothes.pants.id;
                }

                break;
              case "shoes":
                offid = this.clothes.shoes.id;
                break;
              case "jumpsuit":
                if (
                  Object.prototype.hasOwnProperty.call(this.clothes, "jumpsuit")
                ) {
                  offid = this.clothes.jumpsuit.id;
                }
                break;
              case "coat":
                if (
                  Object.prototype.hasOwnProperty.call(this.clothes, "coat")
                ) {
                  offid = this.clothes.coat.id;
                }
                break;
            }

            console.log(offid);
            var postdata;

            if (offid == null) {
              postdata = {
                on_closet_id: this.nchangeClothes.id,
                // off_closet_id:offid
              };
            } else {
              postdata = {
                on_closet_id: this.nchangeClothes.id,
                off_closet_id: offid,
              };
            }
            if (offid == this.nchangeClothes.id) {
              alert("已經穿在身上了!");
              return;
            }

            console.log(postdata);
            this.$http
              .patch(
                tools.apiUrl + "lions/" + this.lion.id + "/wear",
                postdata,
                {
                  emulateJSON: true,
                  headers: {
                    authorization: "Bearer " + tools.getCookie("token"),
                  },
                }
              )
              .then(
                (response) => {
                  this.top_item_content = "更換成功";
                  this.itemshowbool = true;
                  this.refreshView();
                },
                (response) => {
                  console.log(response);
                  console.log(postdata);
                  // this.$parent.alert("網路連線問題，請再試一次!");
                }
              );
          }
          // else if (Object.prototype.hasOwnProperty.call(this.nchangeClothes, 'cloth') && this.nchangeClothes.cloth.class == "special") {
          //   alert("尚未開放!");
          // }
          else {
            offid = this.clothes.deco.id;
            postdata = {
              on_deco_id: this.nchangeClothes.id,
              off_deco_id: offid,
            };
            if (offid == this.nchangeClothes.id) {
              alert("已經穿在身上了!");
              return;
            }
            this.$http
              .patch(
                tools.apiUrl + "lions/" + this.lion.id + "/wearDeco",
                postdata,
                {
                  emulateJSON: true,
                  headers: {
                    authorization: "Bearer " + tools.getCookie("token"),
                  },
                }
              )
              .then(
                (response) => {
                  this.top_item_content = "更換成功";
                  this.itemshowbool = true;
                  this.refreshView();
                },
                (response) => {
                  console.log(response);
                  console.log(postdata);
                  // this.$parent.alert("網路連線問題，請再試一次!");
                }
              );
          }

          break;
      }
    },
    top_itemcheck() {
      this.itemshowbool = false;
      this.topscenebool = false;
      this.badgebool = false;
      this.missionshow = false;
      this.foodbool = false;
      this.specialbool = false;
      this.fooditemshowbool = false;
      this.feeditemshowbool = false;
      this.top_contentc_btn = "確認";
      this.top_contentg_btn = "贈送";
    },

  },
};
</script>


