.progress_bar {
  width: 80%;
  height: 100%;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(255, 255, 255);
  border-radius: 13px;
  background-color: rgb(200, 200, 200);
}

.exp_progress {
  background: rgb(135, 230, 73);
  height: 100%;
  border-radius: 13px;
}

span {
  color: rgb(255, 255, 255);
  font-size: 2vmin;
}

.data_text {
  color: rgb(255, 255, 255);
  font-size: 2.5vmin;
  font-weight: 600;
  letter-spacing: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.lion_body.boychild {
  background-image: url("../assets/boy_no-01.svg");
}

.lion_body.boyadult {
  background-image: url("../assets/adult_lion_male-01.svg");
}

.lion_body.girlchild {
  background-image: url("../assets/girl_no-01.svg");
}

.lion_body.girladult {
  background-image: url("../assets/adult_lion_female-01.svg");
}

.lion_body.boynohead {
  background-image: url("../assets/adult_lion_male_nohead-01.svg");
}

.lion_body.girlnohead {
  background-image: url("../assets/adult_lion_female_nohead-01.svg");
}

.backpack_icon {
  background-image: url("../assets/backpack-01.svg");
}

.clothset_icon {
  background-image: url("../assets/clothset-01.svg");
}

.mission_icon {
  background-image: url("../assets/mission-01.svg");
}

.boxbg {
  background: linear-gradient(rgba(17, 88, 142, 0), rgba(0, 154, 212, 1));
  margin-left: 0.5%;
  border: solid 2px rgb(197, 231, 237);
}

::-webkit-scrollbar {
  color: rgb(255, 255, 255);
  font-size: 4.2vmin;
  font-weight: 600;
  text-align: center;
  width: 12px;
}

::-webkit-scrollbar-thumb,
.bookframe>>> ::-webkit-scrollbar-thumb {
  background: rgb(130, 191, 238);
  border-radius: 10px;
}

::-webkit-scrollbar-track,
.bookframe>>> ::-webkit-scrollbar-track {
  background-color: rgb(50, 50, 50);
  border: 2px solid rgb(255, 255, 255);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: rgb(186, 222, 250);
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: rgb(50, 50, 50);
  border: 2px solid rgb(255, 255, 255);
  border-radius: 10px;
}

.scrollbar {
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 180px;
  width: 90%;
}

.bookframe>>>.scrollbar {
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 250px;
  width: 100%;
}

.scrollbarSet {
  flex: 2;
  max-height: 100%;
  width: 95%;
  position: relative;
  top: -5vh;
}

/*物品欄的物件 */
#bookitembottom>>>input,
input {
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

#bookitembottom>>>input[type="radio"],
input[type="radio"] {
  display: none;
}

input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.boxradiogroup input[type="radio"]+label span {
  line-height: 60px;
  font-size: 2vmin;
  height: 60px;
  border-radius: 50px;
  padding: 5% 12%;
  font-weight: 600;
  letter-spacing: 2px;
  color: rgb(255, 255, 255);
  background: rgb(60, 60, 60);
  cursor: pointer;
}

.boxradiogroup input[type="radio"]:checked+label span {
  background: rgb(123, 212, 232);
  border: 2px solid rgb(255, 255, 255);
}

#bookitembottom>>>input[type="radio"]+label span,
input[type="radio"]+label span {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 80px;
  cursor: pointer;
}

