/*遊戲畫面STYLE*/
@media screen and (min-width: 1920px) {
  .bigWidth {
    flex: 4;
  }

  .bigHeight {
    flex: 7;
  }

  .lion_name span {
    background-repeat: no-repeat;
    background-position: center;
    padding: 12px;
  }

  .level_num {
    font-size: 3.5vmin;
  }

  .cancelBtn,
  .sendBtn {
    position: relative;
    top: -4vh;
  }

  #lion {
    position: relative;
    height: 100%;
    top: -5%;
    flex: 1.5;
  }

  .lion_body {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  .backpack_icon,
  .clothset_icon,
  .mission_icon {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
    cursor: pointer;
    height: 100%;
    margin-left: 0.5%;
  }

  .missionHeight {
    height: 240;
  }

  .heightSet {
    font-size: 2.5vmin;
  }
}

@media screen and (min-width: 1440px) and (max-width: 1919px) {
  .bigWidth {
    flex: 5;
  }

  .bigHeight {
    flex: 7;
  }

  .boxradiogroup {
    flex: 6;
  }

  .lion_name span {
    background-repeat: no-repeat;
    background-position: center;
    padding: 12px;
  }

  #lion {
    position: relative;
    height: 100%;
