<template>
  <div id="giftbox_group">
    <div id="giftbox_page" v-show="giftboxbool">
      <div class="flexbox w-100 h-100"></div>
      <div class="box w-100 h-100" style="flex: 2">
        <div class="flexbox w-100 h-100"></div>
        <div class="giftbox_frame flex-c flexbox w-100 h-100" style="flex: 3">
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
          <div class="flexbox flex-r w-100 h-100">
            <div class="h-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
            <div class="flexbox w-100 h-100 flex-c">
              <div class="flexbox w-100 h-100 position-r">
                <div class="back_btn w-100 h-100 pointer" @click="gifbox_close(false)"></div>
                <div class="close_btn w-100 h-100" @click="gifbox_close(false)"></div>
              </div>
              <div class="flexbox flex-r w-100 h-100" style="flex: 3">
                <div class="flexbox w-100 h-100 pointer propsbtn" @click="showbox('props')">
                  <div id="redDot" v-if="propsdotbool"></div>
                </div>
                <div class="flexbox w-100 h-100 pointer clothbtn" @click="showbox('cloth')">
                  <div id="redDot" v-if="clothdotbool"></div>
                </div>
                <div class="flexbox w-100 h-100 pointer promoCodebtn" @click="showbox('promoCode')">
                </div>
              </div>
              <div class="flexbox w-100 h-100"></div>
            </div>
            <div class="h-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
          </div>
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>
    <div id="propsbox" v-show="propsbool">
      <div class="flexbox w-100 h-100"></div>
      <div class="giftbox_frame box w-100 h-100" style="flex: 2">
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 flex-c" style="flex: 3">
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-c">
            <div class="flexbox w-100 h-100 position-r">
              <div class="back_btn" @click="showbox('giftbox')"></div>
              <div class="close_btn" @click="showbox('giftbox')"></div>
            </div>
            <div class="w-100 h-100 flex-c scrollbar d-flex" v-bind:style="{ 'max-height': scrollbox_h + 'px' }"
              v-html="checkprops()"></div>
            <div class="flexbox w-100 h-100 flex-r">
              <div class="flexbox w-100 h-100">
                <button class="getBtn" @click="getgift('propsboxradio')">
                  領取
                </button>
              </div>
              <div class="flexbox w-100 h-100">
                <button class="sendBtn" @click="sendgift()">贈送</button>
              </div>
            </div>
          </div>
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>
    <div id="clothbox" v-show="clothbool">
      <div class="flexbox w-100 h-100"></div>
      <div class="giftbox_frame box w-100 h-100" style="flex: 2">
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 flex-c" style="flex: 3">
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-c">
            <div class="flexbox w-100 h-100 position-r">
              <div class="back_btn" @click="showbox('giftbox')"></div>
              <div class="close_btn" @click="showbox('giftbox')"></div>
            </div>
            <div class="w-100 h-100 flex-c scrollbar d-flex" v-bind:style="{ 'max-height': scrollbox_h + 'px' }"
              v-html="checkcloth()"></div>
            <div class="flexbox w-100 h-100 flex-r">
              <div class="flexbox w-100 h-100">
                <button class="getBtn" @click="getgift('clothboxradio')">
                  領取
                </button>
              </div>
              <div class="flexbox w-100 h-100">
                <button class="sendBtn" @click="sendgift()">贈送</button>
              </div>
            </div>
          </div>
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>
    <div id="promoCodebox" v-show="promoCodebool">
      <div class="flexbox w-100 h-100"></div>
      <div class="promoCodeFrame box w-100 h-100" style="flex: 2">
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 flex-c" style="flex: 3">
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-c">
            <div class="flexbox w-100 h-100 position-r">
              <div class="back_btn" @click="showbox('giftbox')"></div>
              <div class="close_btn" @click="showbox('giftbox')"></div>
            </div>
            <div class="flexbox w-100 h-100" style="flex: 3"></div>
            <div class="textStyle flexbox w-100 h-100">您的優惠碼:</div>
            <div class="flexbox w-100 h-100">
              <input class="enter_bg" type="text" name="" id="codebox" maxlength="16" />
            </div>
            <div class="flexbox w-100 h-100" style="flex: 3">
              <button class="checkBtn" @click="getcode()">確認</button>
            </div>
          </div>
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>
    <div id="giftboxitem" v-show="itembool">
      <div class="flexbox" style="flex: 2"></div>
      <div class="flexSet flexbox w-100 h-100 flex-c">
        <div class="flexbox"></div>
        <div class="item_frame flexbox w-100 h-100 flex-c">
          <div class="w-100" v-bind:style="{ height: item_outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="h-100" v-bind:style="{ width: item_outbox_w + 'px' }"></div>
            <div id="itemcontent" class="flexbox w-100 h-100 flex-c" v-html="item(giftitemname, giftitemquantity)"
              @click="initemgroup"></div>
            <div class="h-100" v-bind:style="{ width: item_outbox_w + 'px' }"></div>
          </div>

          <div class="w-100" v-bind:style="{ height: item_outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox"></div>
      </div>
      <div class="flexbox" style="flex: 2"></div>
    </div>
  </div>
</template>
<script>
import tools from "../lib/tools.js";

export default {
  name: "gift_box",
  emits: ["gifbox_close"],
  setup(_, { emit }) {
    function gifbox_close(value) {
      emit("gifbox_close", value);
    }

    return { gifbox_close };
  },
  data() {
    return {
      outbox_h: 20,
      outbox_w: 0,
      scrollbox_h: 280,
      item_outbox_h: 20,
      item_outbox_w: 0,
      giftboxbool: true,
      propsbool: false,
      clothbool: false,
      promoCodebool: false,
      itembool: false,
      propsdotbool: false,
      clothdotbool: false,

      getprops: [],
      getcloth: [],
      getfriendgift: [],
      itemtype: "",
      itemnum: 0,
      giftitemname: "",
      giftitemquantity: 0,
    };
  },
  mounted() {
    // this.$http
    //   .get(tools.apiUrl + "missions/1/codes", {
    //     headers: { authorization: "Bearer " + tools.getCookie("token") },
    //   }).then(
    //     (response) => {
    //       console.log("code", response.data);
    //       this.badgearr = response.data;
    //     },
    //     (response) => {
    //       console.log(response);
    //     }
    //   );
  },
  methods: {
    setwidth() {
      var group_h;
      var group_w;
      var group_bg_h;
      var bg_w;
      var group_outbox_h;
      var outbox_w;
      var scrollbox_h;
      var infoitem_h;
      var infoitem_w;
      var infoitem_bg_h;
      var infoitem_bg_w;
      var infoitem_outbox_h;
      var infoitem_outbox_w;

      if (window.innerWidth > 1920) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = window.innerHeight / 3;
        infoitem_w = window.innerWidth / 5;
      } else if (window.innerWidth >= 1440 && window.innerWidth < 1920) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 1024 && window.innerWidth < 1440) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 3) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 768 && window.innerWidth < 1024) {
        group_h = (window.innerHeight / 4.5) * 2.5;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      } else if (window.innerWidth >= 740 && window.innerWidth < 768) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      } else if (window.innerWidth >= 720 && window.innerWidth < 740) {
        group_h = (window.innerHeight / 4) * 2;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 654 && window.innerWidth < 720) {
        group_h = (window.innerHeight / 4) * 2;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 425 && window.innerWidth < 654) {
        group_h = (window.innerHeight / 4.5) * 2.5;
        group_w = (window.innerWidth / 3.5) * 1.5;

        infoitem_h = (window.innerHeight / 4.5) * 2.5;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      }

      group_bg_h = (group_w * 150) / 252;
      bg_w = (group_h * 252) / 150;
      scrollbox_h = (group_w * 93) / 252;
      group_outbox_h = (group_h - group_bg_h) / 2;
      outbox_w = (group_w - bg_w) / 2;

      infoitem_bg_w = (infoitem_h * 178) / 150;
      infoitem_bg_h = (infoitem_w * 150) / 178;
      infoitem_outbox_w = (infoitem_w - infoitem_bg_w) / 2;
      infoitem_outbox_h = (infoitem_h - infoitem_bg_h) / 2;
      console.log(
        group_h,
        group_w,
        group_outbox_h,
        infoitem_outbox_h,
        infoitem_outbox_w
      );
      if (group_outbox_h > 0) {
        this.outbox_h = group_outbox_h;
      } else {
        this.outbox_h = 0;
      }
      if (outbox_w < 0) {
        this.outbox_w = 0;
      } else {
        this.outbox_w = outbox_w;
      }
      this.scrollbox_h = scrollbox_h;
      if (infoitem_outbox_h < 0) {
        this.item_outbox_h = 0;
      } else {
        this.item_outbox_h = infoitem_outbox_h;
      }
      if (infoitem_outbox_w < 0) {
        this.item_outbox_w = 0;
      } else {
        this.item_outbox_w = infoitem_outbox_w;
      }
    },
    refreshView() {
      //抓禮物盒
      this.$http
        .get(tools.apiUrl + "players/" + tools.getCookie("id") + "/giftBox", {
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        })
        .then(
          (response) => {
            console.log("giftBox", response.data);
            this.getprops = [];
            this.getfriendgift = [];
            this.getcloth=[];
            this.itemnum = response.data.length;
            for (var i = 0; i < response.data.length; i++) {
              switch (response.data[i].type) {
                case "mission complete":
                  if(response.data[i].content.present.type=="cloth"){
                    this.getcloth.push(response.data[i]);
                  }else{
                  this.getprops.push(response.data[i]);
                  }

                  break;
                case "receive gift":
                  if (response.data[i].content.present.type=="cloth") {
                    this.getcloth.push(response.data[i]);
                  } else {
                    this.getfriendgift.push(response.data[i]);
                  }

                  break;
              }
            }
            console.log(this.getprops);
            if (this.getprops.length + this.getfriendgift.length != 0) {
              this.propsdotbool = true;
            } else {
              this.propsdotbool = false;
            }
            if(this.getcloth.length>0){
              this.clothdotbool=true;
            }else{
              this.clothdotbool=false;
            }
            if (this.itemnum == 0) {
              this.$parent.$refs.rightmenu.giftboxdotbool = false;
            } else {
              this.$parent.$refs.rightmenu.giftboxdotbool = true;
            }
          },
          (response) => {
            console.log(response);
          }
        );
    },
    showbox(value) {
      switch (value) {
        case "giftbox":
          this.giftboxbool = true;
          this.propsbool = false;
          this.clothbool = false;
          this.promoCodebool = false;
          break;
        case "props":
          this.giftboxbool = false;
          this.propsbool = true;
          this.clothbool = false;
          this.promoCodebool = false;
          break;
        case "cloth":
          this.giftboxbool = false;
          this.propsbool = false;
          this.clothbool = true;
          this.promoCodebool = false;
          break;
        case "promoCode":
          this.giftboxbool = false;
          this.propsbool = false;
          this.clothbool = false;
          this.promoCodebool = true;
          break;
      }
    },
    checkprops() {
      var innerhtml = "";
      var num = this.getprops.length;
      for (var i = 0; i < this.getprops.length; i++) {
        innerhtml += `
                    <div class="heightSet w-100 flexbox">
                        <input id="getprops${i}" type="radio" name="propsboxradio" value="${this.getprops[i].id}" />
                        <label for="getprops${i}" class="w-100">
                            <span class="white">獲得通關「${this.getprops[i].content.mission.title}」任務道具</span>
                        </label>
                    </div>
                `;
      }
      this.getfriendgift.forEach(function (element, index) {
        innerhtml += `
                    <div class="heightSet w-100 flexbox">
                        <input id="getprops${num + index}" type="radio" name="propsboxradio" value="${element.id}" />
                        <label for="getprops${num + index}" class="w-100">
                            <span class="white">獲得玩家「${element.content.player.account}」的好友贈禮</span>
                        </label>
                    </div>
                `;
      })
      return innerhtml;
    },
    checkcloth() {
      var innerhtml = "";
      for (var i = 0; i < this.getcloth.length; i++) {
        innerhtml += `
                    <div class="heightSet w-100 flexbox">
                        <input id="getcloth${i}" type="radio" name="clothboxradio" value="${this.getcloth[i].content.present.cloth.name}" />
                        <label for="getcloth${i}" class="w-100">
                            <span class="white">獲得「${this.getcloth[i].content.present.cloth.name}」特殊服飾</span>
                        </label>
                    </div>
                `;
      }
      return innerhtml;
    },
    getgift(radioname) {
      var radioitem = document.getElementsByName(radioname);
      var giftid;
      for (var i = 0; i < radioitem.length; i++) {
        if (radioitem[i].checked) {
          console.log(radioitem[i].value);
          if (radioitem[i].value != ""&&radioname=="propsboxradio") {
            for (var j = 0; j < this.getprops.length; j++) {
              if (this.getprops[j].id == radioitem[i].value) {
                giftid = this.getprops[j].id;
                this.giftitemname = this.getprops[j].content.present.item.data.name;
                this.giftitemquantity = this.getprops[j].content.present.quantity;
                // this.itemtype = radioitem[i].value;
                // this.itembool = true;
                this.$http
                  .patch(
                    tools.apiUrl +
                    "players/" +
                    tools.getCookie("id") +
                    "/giftBox/" +
                    giftid,
                    {
                      player: parseInt(tools.getCookie("id")),
                      giftBox: giftid,
                    },
                    {
                      emulateJSON: true,
                      headers: {
                        authorization: "Bearer " + tools.getCookie("token"),
                      },
                    }
                  )
                  .then(
                    (response) => {
                      this.itembool = true;
                    },
                    (response) => {
                      console.log(response);
                    }
                  );
              }
            }
            for (var k = 0; k < this.getfriendgift.length; k++) {
              if (parseInt(radioitem[i].value) == this.getfriendgift[k].id) {
                giftid = this.getfriendgift[k].id;
                console.log(giftid, this.getfriendgift[k].content.present.item.data.name, this.getfriendgift[k].content.present.quantity)
                this.giftitemname = this.getfriendgift[k].content.present.item.data.name;
                this.giftitemquantity = this.getfriendgift[k].content.present.quantity;

                // this.itemtype ="getfriendgift";
                this.$http
                  .patch(
                    tools.apiUrl +
                    "players/" +
                    tools.getCookie("id") +
                    "/giftBox/" +
                    giftid,
                    {
                      player: parseInt(tools.getCookie("id")),
                      giftBox: giftid,
                    },
                    {
                      emulateJSON: true,
                      headers: {
                        authorization: "Bearer " + tools.getCookie("token"),
                      },
                    }
                  )
                  .then(
                    (response) => {

                      this.itembool = true;
                    },
                    (response) => {
                      console.log(response);
                    }
                  );
              }
            }
            
          }else if(radioitem[i].value != ""&&radioname=="clothboxradio"){
            for(var q=0;q<this.getcloth.length;q++){
              if(radioitem[i].value==this.getcloth[q].content.present.cloth.name){
                giftid=this.getcloth[q].id;
                this.giftitemname = this.getcloth[q].content.present.cloth.data.name;
                this.giftitemquantity = this.getcloth[q].content.present.quantity;

                this.$http
                  .patch(
                    tools.apiUrl +
                    "players/" +
                    tools.getCookie("id") +
                    "/giftBox/" +
                    giftid,
                    {
                      player: parseInt(tools.getCookie("id")),
                      giftBox: giftid,
                    },
                    {
                      emulateJSON: true,
                      headers: {
                        authorization: "Bearer " + tools.getCookie("token"),
                      },
                    }
                  )
                  .then(
                    (response) => {

                      this.itembool = true;
                    },
                    (response) => {
                      console.log(response);
                    }
                  );
              }
            }
          }
        }
      }
    },
    sendgift() {
      alert("請先領取!");
    },
    item(name, quantity) {
      var innerhtml = "";

      innerhtml = `
                <div class="flexbox w-100 h-100"></div>
                <div class="picSet flexbox w-100 h-100 position-r pic ${name}pic">
                    <div class="number_bg">${quantity}</div>
                </div>
                <div class="flexbox w-100 h-100 flex-c">
                    <p class="success">領取成功</p>
                </div>
                <div class="flexbox w-100 h-100">
                    <button class="send_confirmBtn" id="giftbox_check">確認</button>
                </div>
            `;

      return innerhtml;
    },
    initemgroup(e) {
      let clickedElId = e.target.id;
      switch (clickedElId) {
        case "giftbox_check":
          this.itembool = false;
          break;
      }
      this.refreshView();
    },
    getcode() {
      var codevalue = document.getElementById('codebox').value;
      this.$http
        .patch(tools.apiUrl + "players/" + tools.getCookie('id') + "/exchange", { code: codevalue }, {
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        })
        .then(
          (response) => {
            console.log(response.data);
            document.getElementById('codebox').value = "";
            alert("兌換成功，請至禮物盒領取");
          },
          (response) => {
            console.log(response.response);
            if (response.response.status == 422) {
              if (response.response.data.code == 42202) {
                alert("此優惠碼已被使用過!");
              } else if (response.response.data.code == 42201) {
                alert("查無此優惠碼號碼");
              }

            }

          }
        );
    },
  },
};
</script>
<style scoped>
.pointer {
  cursor: pointer;
}

.w-100,
#giftboxitem>>>.w-100 {
  width: 100%;
}

.h-100,
#giftboxitem>>>.h-100 {
  height: 100%;
}

.flex-r {
  flex-direction: row;
}

.flex-c {
  flex-direction: column;
}

.position-r,
#giftboxitem>>>.position-r {
  position: relative;
}

.d-flex {
  display: flex;
}

.flexbox,
#giftboxitem>>>.flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.parentbox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 1;
}

.back_btn,
.close_btn {
  cursor: pointer;
}

#giftbox_group {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  z-index: 7;
}

#giftbox_page,
#propsbox,
#clothbox,
#promoCodebox {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

#giftboxitem {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  z-index: 2;
  position: absolute;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.giftbox_frame {
  background-image: url("../assets/giftBox_frame.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.promoCodeFrame {
  background-image: url("../assets/coupon_frame-01.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
