<template style="width:100%;height:100%;">
  <rightmenu ref="rightmenu" @setting_btn="settingbtnfun($event)" @info_btn="infofun($event)"
    @friend_btn="friendfun($event)" @gift_btn="giftfun($event)" @log_out="changefun($event)"
    @share_btn="sharefun($event)" />

  <login v-if="activecard === 'login'" @loginmember="changefun($event)" />
  <getlion v-if="activecard === 'getlion'" @teststart="toquestionfun($event)" />
  <question v-if="activecard === 'question'" />
  <introducePage v-show="introducebool" ref="introducepage" @introduce_btn="changefun($event)"
    @back_to_question="backtoquestionfun($event)" />
  <div ref="capture" id="capture" class="image-content">
    <gamescene v-show="gamescenebool" ref="gamescene" @gamescene="alert($event)" />
    <!-- <img id="bgimg" src="http://makers-test.mindnodeair.com/images/backgrounds/background.webp" alt=""> -->
  </div>
  <settingpage ref="settingpage" @setting_close="settingbtnfun($event)" @openpage="toser_pr($event)"
    @tofaq="faqbtnfun($event)" />
  <faqpage v-show="faqbool" ref="faqpage" @faq_close="faqbtnfun($event)" />
  <servicenprivacy v-show="servicenprivacybool" ref="sernpripage" @faq_close="backfaqfun($event)" />
  <informpage v-show="infobool" ref="informpage" @info_close="infofun($event)" />
  <friendshippage v-show="friendshipbool" ref="friendshippage" @friend_close="friendfun($event)" />
  <giftboxpage v-show="giftboxbool" ref="giftboxpage" @gifbox_close="giftfun($event)" />
  <sharepage v-show="sharebool" ref="sharepage" @share_close="sharefun($event)" @share_save="generatorImage($event)" />
  <mobileh v-show="mobilehbool" />
  <windowh v-show="windowhbool" />
  <div class="content" ref="addImage" id="addImage" style="display:none;">
    <!-- <canvas id="canvas"></canvas> -->
  </div>
</template>

<script>
import rightmenu from "./components/right_menu.vue";
import settingpage from "./components/setting_page.vue";
import faqpage from "./components/faq_page.vue";
import servicenprivacy from "./components/service_privacy.vue";
import informpage from "./components/in_form.vue";
import friendshippage from "./components/friendship.vue";
import giftboxpage from "./components/giftbox.vue";
import sharepage from "./components/share_page.vue";
import login from "./components/log_in.vue";
import getlion from "./components/get_lion.vue";
import question from "./components/question.vue";
import introducePage from "./components/introduce_page.vue";
import gamescene from "./components/game_scene.vue";
import mobileh from "./components/check_mobile_horizontal.vue";
import windowh from "./components/check_window_horizontal.vue";
import html2canvas from 'html2canvas';
// import VideoPlayer from '@/components/VideoPlayer.vue';
import tools from "./lib/tools.js";
// import 'video.js/dist/video-js.min.css';
export default {
  data() {
    return {
      activecard: "gamescene",
      loginbool: true,
      getlionbool: false,
      questionbool: true,
      introducebool: false,
      gamescenebool: false,
      mobilehbool: false,
      windowhbool: false,
      faqbool: false,
      servicenprivacybool: false,
      infobool: false,
      friendshipbool: false,
      giftboxbool: false,
      sharebool: false,
      info: "test",
      userinfo: null,
      interval: null,
      saveimg: null,
    };
  },
  components: {
    rightmenu,
    settingpage,
    faqpage,
    servicenprivacy,
    informpage,
    friendshippage,
    giftboxpage,
    sharepage,
    login,
    getlion,
    question,
    introducePage,
    gamescene,
    mobileh,
    windowh,

  },
  created() {

    window.addEventListener("resize", this.myEventHandler);
  },
  unmounted() {
    window.removeEventListener("resize", this.myEventHandler);
  },
  mounted() {

    //  tools.setCookie("uid", "222@gmail.com");

    //lingwen2012@mindnodeair.com 
    //eee@gmail.com >小漁獅 女
    //ddd@gmail.com >小漁獅 男
    //littserver@gmail.com >小漁獅 男
    //222@gmail.com >工程獅 男
    //kkk@gmail.com >工程獅 女
    //meeyuajan@gmail.com >小農獅 女
    //lll@gmail.com >小農獅 男
    //確認是否獲取cookie 沒有回到首頁
    if (!tools.getCookie("uid")) {
      //location.href = tools.testwebUrl;
       location.href = tools.formalUrl;
      return;
    }
    //獲取cookie 執行登入動作
    var postdata = {
      "account": tools.getCookie("uid")
    };
    this.$http.post(tools.apiUrl + "players/login", postdata, { emulateJSON: true })
      .then(response => {
        //紀錄ID auth
        console.log(response.data)
        tools.setCookie("token", response.data.access_token);
        tools.setCookie("id", response.data.player.id);
        this.userinfo = response.data;

        //確認是否有獅子
        if (response.data.player.lions.length > 0) {
          var nlion = response.data.player.lions[0];
          this.togamescenefun(
            "gamescene",
            nlion.occupation,
            nlion.gender,
            nlion
          );
          // if (this.interval == null) {
          //   this.interval = setInterval(() => this.$refs.gamescene.refreshView(), 20000);
          // }
        // var bgimg = document.getElementById('bgimg');
        // bgimg.setAttribute('src','http://makers-test.mindnodeair.com/images/backgrounds/background.webp');
        
        // bgimg.crossOrigin="Anonymous";
        } else {

          this.changefun("getlion");
        }

      }, response => {
        console.error(response);
      });

    this.isMobile();
    this.$refs.introducepage.setwidth();
    this.$refs.gamescene.setwidth();
    this.$refs.settingpage.setwidth();
    this.$refs.faqpage.setwidth();
    this.$refs.sernpripage.setwidth();
    this.$refs.informpage.setwidth();
    this.$refs.friendshippage.setwidth();
    this.$refs.giftboxpage.setwidth();
    window.addEventListener("resize", this.myEventHandler);

  },
  methods: {
    myEventHandler(e) {
      //當視窗改變大小
      console.log(e);
      this.isMobile();
      this.$refs.introducepage.setwidth();
      this.$refs.gamescene.setwidth();
      this.$refs.settingpage.setwidth();
      this.$refs.faqpage.setwidth();
      this.$refs.sernpripage.setwidth();
      this.$refs.informpage.setwidth();
      this.$refs.friendshippage.setwidth();
      this.$refs.giftboxpage.setwidth();
    },
    isMobile() {
      console.log("in", window.innerHeight, window.innerWidth);

      if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        ) &&
        window.innerHeight > window.innerWidth
      ) {
        this.mobilehbool = true;
        this.windowhbool = false;
      } else if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        )
      ) {
        this.mobilehbool = false;
        this.windowhbool = false;
      } else if (
        window.innerHeight >= window.innerWidth ||
        window.innerWidth <= 1060 ||
        window.innerHeight <= 560
      ) {
        this.mobilehbool = false;
        this.windowhbool = true;
      } else {
        this.mobilehbool = false;
        this.windowhbool = false;
      }
    },
    alert(value) {
      //測試用彈出視窗
      alert(value);
    },
    settingbtnfun(value) {
      tools.audioplay();
      this.$refs.settingpage.settingshow = value;
      if (value) {
        this.faqbool = false;
        this.backfaqfun();
        this.infofun(false);
        this.friendfun(false);
        this.giftfun(false);
      }
      //console.log("123", this.settingbool);
    },
    playmusic(play) {
      // if (play) {
      //   this.$refs.VideoPlayer.play();

      // } else {
      //   this.$refs.VideoPlayer.pause();

      // }
      tools.backgroundmusic();
    },
    changefun(value) {
      tools.audioplay();
      console.log(value);


      if (value == "login") {
        //沒有登入/登出，關右邊三個按鈕
        this.$refs.rightmenu.showornotbtn(false);
        console.log("logout");
        // clearInterval(this.interval);
        this.$http.get(tools.apiUrl + "players/logout", {
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        }).then(response => {

        })
        //location.href = tools.testwebUrl;
         location.href = tools.formalUrl;
      } else {
        //登入狀態，開右邊三個按鈕
        this.$refs.rightmenu.showornotbtn(true);
        // console.log("yo");
      }
      if (value != "introduce") {
        this.introducebool = false;
        console.log("no introducebool");
      }
      if (value != "question") {
        this.questionbool = false;
      }
      if (value != "gamescene") {
        this.gamescenebool = false;
      }
      this.activecard = value;
    },
    toquestionfun(value) {
      tools.audioplay();
      this.changefun(value);
      this.questionbool = true;
      console.log("questionbool:", this.questionbool);
    },
    backtoquestionfun(value) {
      tools.audioplay();
      this.changefun(value);
      this.questionbool = true;
      // this.$refs.question.getquestiongroup();
    },
    tointroducefun(value, liontype) {
      tools.audioplay();
      this.changefun(value);
      console.log(liontype);
      this.introducebool = true;
      this.$refs.introducepage.setliontypefun(liontype);
    },
    togamescenefun(value, liontype, maletype, lion) {
      tools.audioplay();
      this.gamescenebool = true;
      this.changefun(value);
      this.$refs.gamescene.setliondata(liontype, maletype, lion);
    },
    faqbtnfun(value) {
      tools.audioplay();
      this.faqbool = value;
      if (value) {
        this.settingbtnfun(false);
      } else {
        this.settingbtnfun(true);
      }
      this.$refs.faqpage.showbox("faq");
    },
    backfaqfun() {
      tools.audioplay();
      this.$refs.settingpage.settingshow = true;
      this.servicenprivacybool = false;
    },
    toser_pr(value) {
      tools.audioplay();
      this.servicenprivacybool = true;
      this.$refs.sernpripage.type = value;
      this.$refs.settingpage.settingshow = false;
    },
    infofun(value) {
      tools.audioplay();
      this.infobool = value;

      if (value) {
        this.settingbtnfun(false);
        this.faqbool = false;
        this.friendfun(false);
        this.servicenprivacybool = false;
        this.giftfun(false);
        this.$refs.informpage.refreshView();
      } else {
        this.$refs.informpage.itembool = false;
      }
    },
    friendfun(value) {
      tools.audioplay();
      this.friendshipbool = value;
      if (value) {
        this.settingbtnfun(false);
        this.infofun(false);
        this.faqbool = false;
        this.servicenprivacybool = false;
        this.giftfun(false);
        this.$refs.friendshippage.refreshView();
        this.$refs.friendshippage.list_rankbool = true;
        this.$refs.friendshippage.searchbool = false;
        this.$refs.friendshippage.$refs.searchinput.value = "";

      }
    },
    giftfun(value) {
      tools.audioplay();
      this.giftboxbool = value;
      if (value) {
        this.$refs.giftboxpage.refreshView();
        this.settingbtnfun(false);
        this.infofun(false);
        this.faqbool = false;
        this.servicenprivacybool = false;
        this.friendfun(false);
      }
    },
    sharefun(value) {
      tools.audioplay();
      this.sharebool = value;
      if (value) {
        this.settingbtnfun(false);
        this.infofun(false);
        this.faqbool = false;
        this.servicenprivacybool = false;
        this.friendfun(false);
        this.giftfun(false);
      }
    },
    generatorImage(value) {
      if (this.gamescenebool) {
        this.$refs.gamescene.boxbool = false;
        this.$refs.gamescene.lionflex = 5;
        var vm = this;

        // this.$refs.gamescene.generatorImage();
          vm.saveimg = setInterval(() => {
            html2canvas(vm.$refs.capture).then(canvas => {

              var context = canvas.getContext('2d');
              var img = new Image();
              var height = window.innerHeight;
              var width = window.innerWidth;
              var liontype = vm.$refs.gamescene.liontype;
              var lionclass = vm.$refs.gamescene.lionclass;
              var liongender = "";
              var lionname = vm.$refs.gamescene.name;
              var myDate = new Date();
              var year = myDate.getFullYear();
              var month = myDate.getMonth() + 1;
              var day = myDate.getDate();
              var hrs = myDate.getHours();
              var mins = myDate.getMinutes();
              var secs = myDate.getSeconds();
              // if (month.length < 2)
              //   month = '0' + month;
              // if (day.length < 2)
              //   day = '0' + day;
              switch (vm.$refs.gamescene.male) {
                case 'boy':
                  liongender = 'male';
                  break;
                case 'girl':
                  liongender = 'female';
                  break;
              }
              context.drawImage(canvas, 0, 0, width, height);
              img.src = require(`@/assets/download/${liontype}_${lionclass}_${liongender}-01.png`);
              console.log(img.src);
              img.width = width;
              img.height = height;
              img.onload = function () {
                context.drawImage(img, 0, 0, width, height);
                context.save();
                canvas.setAttribute('id', 'canvassetimg')
                document.getElementById('addImage').append(canvas);
                if (value) {
                  let imgUri = canvas.toDataURL("image/jpeg")
                  console.log(imgUri);
                  let link = document.createElement('a');
                  link.href = imgUri;
                  link.setAttribute('id', 'downloadimglink');
                  link.setAttribute('download', `download_${lionname}_${year}${month}${day}_${hrs}_${mins}_${secs}.jpg`);
                  link.style.display = 'none';
                  document.body.appendChild(link);
                  link.click();
                  document.getElementById('downloadimglink').remove();

                } else {
                  var shareurl = "https://www.twmakers.com.tw/web/game/game_mex.jsp#gameInId"; //Set desired URL here
                  var shareimg = canvas.toDataURL("image/jpeg"); //Set Desired Image here
                  var encodeurl = encodeURIComponent(shareurl);
                  var encodeimg = encodeURIComponent(shareimg);
                  var encodetext = encodeURIComponent('大家一起來培養梅克獅吧!');
                  window.open('https://www.facebook.com/sharer/sharer.php?u=' + encodeurl, '_blank');
                }
                document.getElementById('canvassetimg').remove();
              }

              clearInterval(vm.saveimg);


            })
          }, 1000)


      } else {
        alert('請先創立角色!');
      }


    },

    share() {

    },
  },
};
</script>

<style>
html,
body,
#app {
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-image: url("@/assets/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

#capture {
  position: relative;
      background-image: url("@/assets/background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

/* #capture #bgimg{
  width:100vw;
  height:100vh;
  position: absolute;
  top:0;
  left:0;
  z-index: -1;

} */

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  box-sizing: border-box;
}

#canvas {
  width: 100vw;
  height: 100vh;
}
</style>
