<template>
  <div id="faqgroup">
    <!-- FAQ -->
    <div id="faq_page" v-show="faqbool">
      <div class="flexbox w-100 h-100"></div>
      <div class="box w-100 h-100" style="flex: 2">
        <div class="flexbox w-100 h-100"></div>
        <div class="faq_frame flex-c flexbox w-100 h-100" style="flex: 3">
          <div
            class="w-100"
            v-bind:style="{ height: game_outbox_h + 'px' }"
          ></div>
          <div class="flexbox flex-r w-100 h-100">
            <div
              class="h-100"
              v-bind:style="{ width: faq_outbox_w + 'px' }"
            ></div>
            <div class="flexbox w-100 h-100 flex-c">
              <div class="flexbox w-100 h-100 position-r">
                <div
                  class="back_btn w-100 h-100 pointer"
                  @click="faq_close(false)"
                ></div>
                <div
                  class="close_btn w-100 h-100"
                  @click="faq_close(false)"
                ></div>
              </div>
              <div class="flexbox flex-r w-100 h-100" style="flex: 3">
                <div
                  class="flexbox w-100 h-100 pointer gameExplanation"
                  @click="showbox('遊戲說明')"
                ></div>
                <div
                  class="flexbox w-100 h-100 pointer levelExplanation"
                  @click="showbox('等級說明')"
                ></div>
                <div
                  class="flexbox w-100 h-100 pointer characterExplanation"
                  @click="showbox('角色說明')"
                ></div>
              </div>
              <div class="flexbox w-100 h-100"></div>
            </div>

            <div
              class="h-100"
              v-bind:style="{ width: faq_outbox_w + 'px' }"
            ></div>
          </div>

          <div
            class="w-100"
            v-bind:style="{ height: game_outbox_h + 'px' }"
          ></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>

    <!--faq三個-->
    <div id="thirdgroup" v-show="groupbool">
      <div class="flexbox w-100 h-100"></div>
      <div class="levelFrame box w-100 h-100" style="flex: 2">
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 flex-c" style="flex: 3">
          <div
            class="w-100"
            v-bind:style="{ height: game_outbox_h + 'px' }"
          ></div>
          <div class="flexbox w-100 h-100 flex-c">
            <div class="flexbox w-100 h-100 position-r">
              <div class="back_btn" @click="showbox('faq')"></div>
              <div class="close_btn" @click="showbox('faq')"></div>
            </div>
            <div class="title flexbox w-100 h-100">{{ captionname }}</div>
            <div
              class="w-100 h-100 flex-c scrollbar"
              style="dispaly: flex"
              v-bind:style="{ 'max-height': scrollbox_h + 'px' }"
              v-html="checkcaption(captionname)"
            ></div>
            <div class="flexbox w-100 h-100"></div>
          </div>
          <div
            class="w-100"
            v-bind:style="{ height: game_outbox_h + 'px' }"
          ></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "faq_page",
  emits: ["faq_close"],
  setup(_, { emit }) {
    function faq_close(value) {
      emit("faq_close", value);
    }
    return { faq_close };
  },
  data() {
    return {
      faq_outbox_w: 0,
      game_outbox_h: 20,
      scrollbox_h: 380,
      captionname: "遊戲說明",
      faqbool: true,
      groupbool: false,
    };
  },
  methods: {
    setwidth() {
      // var faqgroup_h;
      // var faqgroup_w;
      // var faqgroup_bg_w;
      // var faqgroup_bg_h;
      // var faqgroup_bg_outbox_w;

      // faqgroup_h = window.innerHeight / 3;
      // faqgroup_w = window.innerWidth / 5;
      // faqgroup_bg_h = faqgroup_h;
      // faqgroup_bg_w = (faqgroup_bg_h * 252) / 150;
      // faqgroup_bg_outbox_w = (faqgroup_w - faqgroup_bg_w) / 2;

      // this.faq_outbox_w = faqgroup_bg_outbox_w;
      //console.log(faqgroup_h, faqgroup_w, faqgroup_bg_w, this.faq_outbox_w);

      var gamegroup_h;
      var gamegroup_w;
      var gamegroup_bg_h;
      var bg_w;
      var gamegroup_outbox_h;
      var scrollbox_h;
      var outbox_w;
      gamegroup_h = (window.innerHeight / 5) * 3;
      gamegroup_w = (window.innerWidth / 4) * 2;
      gamegroup_bg_h = (gamegroup_w * 150) / 252;
      bg_w = (gamegroup_h * 252) / 150;

      scrollbox_h = (gamegroup_w * 93) / 252;

      gamegroup_outbox_h = (gamegroup_h - gamegroup_bg_h) / 2;
      outbox_w = (gamegroup_w - bg_w) / 2;
      console.log(gamegroup_h, gamegroup_w, gamegroup_outbox_h, scrollbox_h);
      if (gamegroup_outbox_h > 0) {
        this.game_outbox_h = gamegroup_outbox_h;
      } else {
        this.game_outbox_h = 0;
      }
      if (outbox_w < 0) {
        this.faq_outbox_w = 0;
      } else {
        this.faq_outbox_w = outbox_w;
      }
      this.scrollbox_h = scrollbox_h;
    },
    showbox(value) {
      switch (value) {
        case "faq":
          this.faqbool = true;
          this.groupbool = false;
          break;
        default:
          this.faqbool = false;
          this.groupbool = true;
          this.captionname = value;
          break;
      }
      //this.setwidth();
      console.log("click", value);
    },
    checkcaption(value) {
      var innerhtml = "";

      switch (value) {
        case "遊戲說明":
          innerhtml = `
            <div class="flexbox">
              加入會員後，選擇性別、填寫性向測驗，依遊戲系統之性向測驗分類技職職群，可以獲得一隻職群原始梅克獅(區分為男女性別，並有
              小農獅、工程獅、小魚獅職群，共六款原始梅克獅)，透過完成各項網站與實體展場任務，完成任務後獲得食物，將食物餵食梅克獅可
              成長升級。
              每次只能從原始獅開始養成一款，至達人獅後完全養成，才可養成下一款梅克獅。例如，選擇女生原始小農獅後，須養成至女生達人
              小農獅，才可繼續養不同性別職群梅克獅。
            </div>
            <div
              class="flexbox w-100"
              style="flex: 1.5; margin-top: 5%; margin-bottom: 5%"
            >
              <div class="meats flexbox w-100 h-100"></div>
            </div>
            <ul class="flexbox flex-c" style="margin: 0; padding: 0">
              <li>
                肉存糧表：完成活動和任務，獲得食物會先儲存在這，點擊餵食才會換算成經驗值
              </li>
              <li>
                經驗值表：顯示距離下個Lv還需多少經驗值；超過3天不登入，第4天開始每一天會少10經驗值，養成一款需10500經驗值
              </li>
            </ul>
          `;
          break;
        case "等級說明":
          innerhtml = `
                        <div class="levelPart flexbox w-100 h-100 flex-r">
                <div class="flexbox levelText w-100 h-100"></div>
                <div class="textbg flexbox w-100 h-100">
                  <div class="scrollbar2 w-100 h-100">
                    <p class="normalText">
                      <span class="titleText">第一階段-</span>
                      原始梅克獅(Lv1~10)，此階段每50經驗值可升一個Lv
                    </p>

                    <p class="normalText">
                      <span class="titleText">第二階段-</span>
                      學子梅克獅(Lv11~20)，此階段每100經驗值可升一個Lv
                    </p>
                    <p class="normalText">
                      <span class="titleText">第三階段-</span>
                      菜鳥梅克獅(Lv21~30)，此階段每150經驗值可升一個Lv
                    </p>
                    <p class="normalText">
                      <span class="titleText">第四階段-</span>
                      匠人梅克獅(Lv31~40)，此階段每200經驗值可升一個Lv
                    </p>
                    <p class="normalText">
                      <span class="titleText">第五階段-</span>
                      職人梅克獅(Lv41~50)，此階段每250經驗值可升一個Lv
                    </p>
                    <p class="normalText">
                      <span class="titleText">第六階段-</span>
                      達人梅克獅(Lv51~60)，此階段每300經驗值可升一個Lv
                    </p>
                  </div>
                </div>
              </div>
              <div class="block flexbox w-100 h-100 box">
                <div class="borderBottom w-100 parentbox">
                  <div class="levelTitle flexbox">原始 Lv1~10</div>
                  <div class="levelContent flexbox">
                    對於世界有無盡好奇，父母帶領著孩子去探索。<br />
                    (學齡前~小學)
                  </div>
                </div>
                <div class="borderBottom w-100 parentbox">
                  <div class="levelTitle flexbox">學子 Lv11~20</div>
                  <div class="levelContent flexbox">
                    開始廣泛地學習各項知識，也透過各種探索和體驗去了解世界，大部分透過老師引領與教育。<br />
                    (小學~高中)
                  </div>
                </div>
                <div class="borderBottom w-100 parentbox">
                  <div class="levelTitle flexbox">菜鳥 Lv21~30</div>
                  <div class="levelContent flexbox">
                    最青春無敵的年紀，開始知道了自己未來志向，要念書還會打工。畢業後從事第一份正式工作，
                    在職場上是最青澀的存在，有許多職場前輩教導。<br />
                    (大學~職場新鮮人)
                  </div>
                </div>
                <div class="borderBottom w-100 parentbox">
                  <div class="levelTitle flexbox">匠人 Lv31~40</div>
                  <div class="levelContent flexbox">
                    從事職業已一段時間，對於工作時熟稔，技藝高超但追求更高的知識、技術或藝術境界。
                  </div>
                </div>
                <div class="borderBottom w-100 parentbox">
                  <div class="levelTitle flexbox">職人 Lv41~50</div>
                  <div class="levelContent flexbox">
                    透過熟練的技術與雙手打造作品/產品，要成為職人，一般需要好幾年，甚至與數十年的時間
                    專注在領域上，對於自己的作品擁有相當的自信，不因為金錢或時間的限制而妥協。
                  </div>
                </div>
                <div class="borderBottom w-100 parentbox">
                  <div class="levelTitle flexbox">達人 Lv51~60</div>
                  <div class="levelContent flexbox">
                    在某一領域非常專業、擁有許多豐富經驗、技術和知識，是專業領域中出類拔萃的存在，也被稱為專家。
                  </div>
                </div>
              </div>
          `;
          break;
        case "角色說明":
          innerhtml = `
                      <div class="flexbox textbg flex-r">
              <div class="flexbox scrollbar2 flex-r w-100" style="color: white">
                我是小農獅，平常喜歡戶外活動、喜歡親近大自然、也喜歡觀察和照顧動植物。<br />
                <br />#小農獅關鍵字: 愛好冒險、觀察、創意手作<br />
                <br />血型A: 細心負責、工作腳踏實地、喜歡穩定<br />
                相關類群: 農業群、食品群
              </div>
              <div class="lionPictures_farmer flexbox w-100 h-100"></div>
            </div>
            <div class="flexbox textbg flex-r">
              <div class="scrollbar2 flex-r w-100 h-100" style="color: white">
                我是工程獅，喜歡尋找解決問題的方法，也對工程、科技有很大的興趣；平常會自己拆組電器。<br />
                <br />#工程獅關鍵字：邏輯、數理推理、工程科技、手作<br />
                <br />AB型血：理解速度快、喜歡挑戰、冷靜沈穩<br />
                相關群類：機械群、動力機械群、電機與電子群、化工群、土木與建築群等
              </div>
              <div class="lionPictures_engineer flexbox w-100 h-100"></div>
            </div>
            <div class="flexbox textbg flex-r">
              <div class="scrollbar2 flex-r w-100 h-100" style="color: white">
                我是小漁狮,喜愛觀察水中自然生態,平常也喜歡去釣魚,夢想是能夠擁有自己的一艘船!<br />
                <br />#小漁獅關鍵字:觀察、冒險、不怕水、熱愛海洋<br />
                <br />B型血:討厭一成不變、愛好廣泛、外向樂觀<br />
                相關群類：水產群、海事群
              </div>
              <div class="lionPictures_fisher flexbox w-100 h-100"></div>
            </div>
          `;
          break;
      }

      return innerhtml;
    },
  },
};
</script>

