<template>
  <!-- 設定 -->
  <div id="setting_page" v-show="settingshow">
    <div class="flexbox"></div>
    <div class="box">
      <div class="flexbox" style="flex: 2"></div>
      <div class="setting_frame parentbox">
        <div class="h-100" v-bind:style="{ width: se_outbox_w + 'px' }"></div>
        <div class="flexbox w-100 h-100 flex-c">
          <div class="w-100" v-bind:style="{height:se_outbox_h+'px'}"></div>
          <div class="flexbox w-100 position-r">
            <div class="close_btn pointer" @click="setting_close(false)"></div>
          </div>
          <div class="flexbox voiceBtn w-100">
            <input
              id="music"
              type="checkbox"
              name="musicBtn"
              value="1"
              class="pointer"
              
            />
            <label for="musicBtn" style="width: 100%; height: 100%">
              <span @click="settingmusic()"></span>
            </label>

            <input
              id="sound"
              type="checkbox"
              checked
              name="soundeffectBtn"
              value="2"
              class="pointer"
            />
            <label for="soundBtn" style="width: 100%; height: 100%">
              <span @click="settingsound()"></span>
            </label>
          </div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="flexbox"></div>
            <div
              class="flexbox faqBtn pointer"
              style="flex: 2"
              @click="tofaq(true)"
            ></div>
            <div class="flexbox"></div>
          </div>
          <div class="flexbox w-100 items">
            <div class="flexbox pointer contact" @click="sendemail()">
              聯絡我們
            </div>
            <div class="flexbox pointer service" @click="openpage('service')">
              服務條款
            </div>
            <div class="flexbox pointer privacy" @click="openpage('privacy')">
              隱私權政策
            </div>
          </div>
          <div class="w-100" v-bind:style="{height:se_outbox_h+'px'}"></div>
        </div>
        <div class="h-100" v-bind:style="{ width: se_outbox_w + 'px' }"></div>
      </div>
      <div class="flexbox" style="flex: 2"></div>
    </div>
    <div class="flexbox"></div>
  </div>
</template>

<script>
import tools from "@/lib/tools";

export default {
  name: "setting_page",
  emits: ["setting_close", "openpage", "tofaq"],
  setup(_, { emit }) {
    function setting_close(value) {
      emit("setting_close", value);
    }
    function openpage(value) {
      emit("openpage", value);
    }
    function tofaq(value) {
      emit("tofaq", value);
    }
    return { setting_close, openpage, tofaq };
  },
  data() {
    return {
      se_outbox_w: 20,
      se_outbox_h:0,
      settingshow: false,
    };
  },mounted(){
    if (!tools.getCookie("sound")) {
        document.getElementById("sound").checked = false;
      } else {
        document.getElementById("sound").checked = true;
      }
  },
  methods: {
    setwidth() {
      var settinggroup_h;
      var settinggroup_w;
      var settinggroup_bg_w;
      var settinggroup_bg_h;
      var settinggroup_bg_outbox_w;
      var settinggroup_bg_outbox_h;
      if (window.innerWidth >= 1440) {
        settinggroup_h = window.innerHeight / 3;
        settinggroup_w = window.innerWidth / 5;
      } else if (window.innerWidth >= 1024 && window.innerWidth < 1440) {
        settinggroup_h = (window.innerHeight / 4) * 2;
        settinggroup_w = (window.innerWidth / 7) * 3;
      } else if (window.innerWidth >= 768 && window.innerWidth < 1024) {
        settinggroup_h = (window.innerHeight / 6) * 4;
        settinggroup_w = (window.innerWidth / 7) * 3;
      } else if (window.innerWidth < 768) {
        settinggroup_h = (window.innerHeight / 8) * 6;
        settinggroup_w = (window.innerWidth / 8.5) * 4.5;
      }

      settinggroup_bg_h = (settinggroup_w * 150) / 166;
      settinggroup_bg_w = (settinggroup_h * 166) / 150;
      settinggroup_bg_outbox_w = (settinggroup_w - settinggroup_bg_w) / 2;
      settinggroup_bg_outbox_h=(settinggroup_h-settinggroup_bg_h)/2;
      if (settinggroup_bg_outbox_w > 0) {
        this.se_outbox_w = settinggroup_bg_outbox_w;
      } else {
        this.se_outbox_w = 0;
      }
      if(settinggroup_bg_outbox_h>0){
        this.se_outbox_h=settinggroup_bg_outbox_h;
      }else{
        this.se_outbox_h=0;
      }
      // console.log(settinggroup_bg_outbox_w);
    },
    sendemail() {
      window.open(
        "https://mail.google.com/mail/u/0/?fs=1&to=koichiyamamoto@mindnodeair.com&tf=cm",
        "_blank"
      );
    },
    settingmusic() {
      var musicbool = document.getElementById("music").checked;
      if (musicbool) {
         this.$parent.playmusic(false);
        document.getElementById("music").checked = false;
      } else {
          this.$parent.playmusic(true);
        document.getElementById("music").checked = true;
      }
      console.log(musicbool);
      tools.setCookie("backgroundmusic",!musicbool);
    },
    settingsound() {
      var soundbool = document.getElementById("sound").checked;
      if (soundbool) {
        document.getElementById("sound").checked = false;
      } else {
        document.getElementById("sound").checked = true;
      }
      tools.setCookie("sound",!soundbool);
    },
  },
};
</script>

