             </p>
             <p>會員註冊及遊戲服務之申請，如甲方為無行為能力人，應由其法定代理人為之；如為限制行為能力人，應得法定代理人之同意。您若與本遊戲因此《會員協議》或其補充協議所涉及的有關事宜發生爭議或者糾紛，雙方可以良好協商解決；協商不成，您完全同意將其回報本遊戲所在地的法院訴訟解決。
             </p>
              <ul class="ulgroup">
                <li>定義</br>本條款使用下列各項用語：
                <ul>
                  <li>「帳號」係指本單位於用戶開始使用本服務時所發行之用以辨識用戶的文字列或其他標識。其中，本單位基於用戶申請所設定的標識符稱為「用戶ID」。</li>
                  <li>「個別使用條款」係指與本服務有關而有別於本條款，以「條款」、「準則」、「政策」等名稱由本單位所分發或公布的書面，其中包含本單位所擬定之遊戲管理規則。「遊戲管理規則」係指本單位於遊戲內容資訊或常見問答中所公告或上傳對於限制或禁止使用遊戲之相關準則。</li>
                  <li>「內容」係指文字、語音、音樂、圖片、影片、軟體、程式、代碼及其他資訊等內容。</li>
                  <li>「本內容」係指可透過本服務連結的內容。</li>
                  <li>「投稿內容」係指用戶投稿、傳送、上傳至本服務的內容。</li>
                </ul>
                </li>
                <li>對於本條款的同意表示
                <ul>
                  <li>用戶應遵照本條款規定使用本服務。用戶須有效且不可撤銷地同意本條款，方能使用本服務。以下內容視為本條款之一部分，與本條款具有相同之效力：
                  <ul class="cjkul">
                    <li>由本單位所為有關本服務之廣告或宣傳內容。</li>
