                  <li>本服務一經用戶實際使用，將視為用戶已有效且不可撤銷地同意本條款。</li>
                  <li>本服務有個別使用條款時，除本條款外，用戶亦應遵照個別使用條款規定使用本服務。</li>
                  <li>本條款如有疑義時，應為有利於用戶之解釋。</li>
                </ul>
                </li>
                <li>本條款之變更</br>本單位修改本條款或個別使用條款時，應於本單位官網之首頁、遊戲登入頁面或購買頁面公告之，並依用戶登錄之通訊資料通知用戶。本單位未依前項進行公告及通知者，本條款或個別使用條款之變更無效。用戶於前段通知到達後十五日內，如：
                <ul class="cjkul">
