                  <li>用戶未設定用戶ID而使用本服務，或使用無用戶ID設定功能之服務時，若進行通信設備的機種變更等行為，遊戲內貨幣、投稿內容等資料將無法轉移到變更後的通信設備。</li>
                  <li>符合前項規定時，用戶購買之遊戲內貨幣或其他有償內容相關之資料以及其他所有與本服務相關之資料，可能因以下原因，在不事先通知用戶的情況下消滅：
