                    <li>用戶得依本單位提供之修改機制變更密碼。本單位人員（含客服人員、遊戲管理員）不得主動詢問用戶之密碼。本單位應於本條款終止後三十日內，保留用戶之帳號與電磁紀錄。</li>
                    <li>本條款非因可歸責用戶之事由而終止者，用戶於前項期間內辦理續用後，有權繼續使用原帳號及附隨於該帳號之電磁紀錄。</li>
                    <li>於第4.7.3條期間屆滿，用戶仍未辦理續用者，本單位得刪除該帳號及附隨於該帳號之所有資料，但法令另有規定者不在此限。</li>
                  </ul>
                  </li>
