                    <li>本單位應於暫時限制遊戲使用權利之時起，即刻以官網公告、簡訊、電子郵件、推播或其他雙方約定之方式通知前項之第三人提出 說明。如該第三人未於接獲通知時起七日內提出說明，本單位應直接回復遭不當移轉之電磁紀錄予用戶，如不能回復時可採其他雙方同意之相當補償方式，並於回復後解除對第三人之限制。但如本單位有提供免費安全裝置（如防盜卡、電話鎖等）而用戶不使用或有其他可歸責於用戶之事由，本單位不負回復或補償責任。</li>
