                    <li>本單位依第4.8.1.條規定限制用戶或第三人之使用權時，在限制使用期間內，本單位不得向用戶或第三人收取費用。</li>
                    <li>用戶如有申告不實之情形致生本單位或第三人權利受損時，應負一切法律責任。</li>
                  </ul>
