                <li>本單位尊重用戶的隱私權。</li>
                <li>本單位依照技職大玩JOB之梅克獅養成遊戲服務隱私權政策及相關法律，妥善處理及保護用戶的隱私資料與個人資料。</li>
                <li>本單位將會以安全的方式管理自用戶處蒐集的資料，並對安全管理採取嚴格的措施。</li>
