                <li>恐有危害公共秩序或善良風俗的行為。</li>
                <li>侵害本公司或第三人的著作權、商標權、專利權等智慧財產權、名譽權、隱私權、其他法令或契約規定權利的行為。</li>
                <li>投稿或傳送內容有過度暴力的表現、性暗示過度露骨的表現、涉及人種、國籍、信仰、性別、社會地位、家世等有關歧視的表現、引誘或助長自殺、自傷行為或濫用藥物的表現、其他有反社會情節使人感到不愉快的表現的行為。</li>
                <li>偽裝成本公司或第三人的行為或故意散布不實資訊的行為。</li>
