                <li>以行銷、宣傳、廣告、招攬或其他營利為目的的行為（經本公司同意者除外）、以性行為或猥褻行為為目的的行為、以與未曾謀面的異性認識或交往為目的的行為、以騷擾或毀謗中傷其他用戶為目的的行為、其他基於與本服務訂定的使用目的不同之目的使用本服務的行為。</li>
                <li>對反社會勢力提供利益或其他協助的行為。</li>
