var tools = {};
import audiowav from "../assets/button.wav";
import backgroundmusic from "../assets/music.mp3";

var audio=new Audio(audiowav);
var music = new Audio(backgroundmusic);
music.loop=true;
//正式機的api
 tools.apiUrl="https://makers.mindnodeair.com/api/";
//tools.apiUrl = "https://makers-test.mindnodeair.com/api/";
tools.testwebUrl = "http://lab2.geneinfo.com.tw/twmakers/home.jsp";
tools.formalUrl="https://www.twmakers.com.tw/home.jsp";
tools.setCookie = function (key, value) {
  var expires = new Date();
  expires.setTime(expires.getTime() + (30 * 24 * 60 * 60 * 1000));
  document.cookie = key + '=' + value + ';expires=' + expires.toUTCString() + ";" + "path=/";
  return true;
}
tools.audioplay= function (key) {
  if(this.getCookie("sound")=="false"){
    return;
  }
  
  if(audio.paused){
  console.log(this.getCookie("sound"));
  audio.play();
  }
}
tools.backgroundmusic=function(key){
  if(this.getCookie("backgroundmusic")=="true"){
    music.pause();
    music.currentTime=0;
  }
  if(this.getCookie("backgroundmusic")=="false"){

    music.play();
    
  }
}
tools.getCookie = function (key) {
  var keyValue = document.cookie.match('(^|;) ?' + key + '=([^;]*)(;|$)');
  return keyValue ? keyValue[2] : null;
}
tools.eraseCookie = function (key) {
  var keyValue = this.getCookie(key);
  this.setCookie(key, keyValue, '-1');
  return true;
}

export default tools