<script>
import tools  from "../lib/tools.js";

export default {
  name: "log_in",
  emits: ["loginmember"],
  setup(_, { emit }) {
    function loginmember(value) {
      emit("loginmember", value);
    }
    return { loginmember };
  },
  methods:{
       goToLogin() {
         location.href =tools.webUrl;
       }
  }
};
</script>


