<template>
  <div class="group">
    <!--下層的結果類型介紹-->
    <div id="introduce_page">
      <div class="flexbox"></div>
      <div class="result box" :class="setliontype">
        <div class="flexbox"></div>
        <div class="flexbox contextgroup">
          <div v-bind:style="{ height: in_outbox_h + 'px' }"></div>
          <div class="btn_pos">
            <div class="close_btn" @click="introduce_btn('getlion')"></div>
          </div>
          <div class="flexbox" style="flex: 2; flex-direction: row">
            <div class="flexbox flex_2" style="flex-direction: column">
              <div class="context flexbox">你是 {{ job_type }}</div>
              <div class="context2 flexbox scrollbar" v-html="content"></div>
              <div class="flexbox"></div>
            </div>
            <div class="flexbox"></div>
          </div>

          <div class="buttons flexbox" style="flex-direction: row; width: 100%">
            <div
              class="retest flexbox"
              id="retestbtn"
              @click="back_to_question('question')"
            ></div>
            <div
              class="name flexbox"
              id="namebtn"
              @click="nameshowbool = true"
            ></div>
          </div>

          <div v-bind:style="{ height: in_outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox"></div>
      </div>
      <div class="flexbox"></div>
    </div>

    <!--疊再上層的取名視窗-->
    <div id="name_window" v-show="nameshowbool">
      <div class="flexbox"></div>
      <div class="window_bg flexbox">
        <div class="name_flex"></div>
        <div class="flexbox">
          <div
            style="height: 100%; position: relative"
            v-bind:style="{ width: n_outbox_w + 'px' }"
          >
            <div class="back_btn" @click="backfun()"></div>
          </div>
          <div class="flexbox" style="flex: 3; flex-direction: column">
            <div class="flexbox"></div>
            <div class="context flexbox">幫它取名字</div>
            <div class="context2 flexbox">我的梅克獅叫:</div>
            <div class="flexbox">
              <input
                id="name_input"
                type="text"
                maxlength="8"
                placeholder="名字最多8個字唷"
              />
            </div>

            <div class="gender_btn flexbox">
              <div class="flexbox">
                <input
                  id="boy"
                  checked="checked"
                  type="radio"
                  name="radio1"
                  value="1"
                />
                <label for="boy" style="width: 100%; height: 100%"
                  ><span></span
                ></label>
              </div>

              <div class="flexbox">
                <input id="girl" type="radio" name="radio1" value="2" />
                <label for="girl" style="width: 100%; height: 100%"
                  ><span></span
                ></label>
              </div>
            </div>
            <div class="flexbox" style="margin-top: 3% flex-direction:row;">
              <div class="flexbox"></div>
              <div class="confirm flexbox" @click="createlionfun()">確認</div>
              <div class="flexbox"></div>
            </div>
            <div class="flexbox"></div>
          </div>
          <div v-bind:style="{ width: n_outbox_w + 'px' }"></div>
        </div>
        <div class="name_flex"></div>
      </div>
      <div class="flexbox"></div>
    </div>
  </div>
</template>

<script>
//import json from "@/assets/json/lion_type";

import tools from "@/lib/tools";

//下層的結果類型介紹
export default {
  name: "IntroducePage",
  emits: ["introduce_btn", "back_to_question"],
  setup(_, { emit }) {
    function introduce_btn(value) {
      emit("introduce_btn", value);
    }
    function back_to_question(value) {
      emit("back_to_question", value);
    }
    return { introduce_btn, back_to_question };
  },
  data() {
    return {
      in_outbox_h: 30,
      n_outbox_w: 10,
      setliontype: "farmer",
      occupation_id:"1",
      lion_type: [],
      job_type: "社會型/實作型",
      content:
        "刻苦耐勞、率真、有耐心，每天辛勤的從家裡到農場照顧農作物以及動物們，日出出門、日落回家，一年365天無論颳風下雨都始終如一。",
      nameshowbool: false,
    };
  },
  mounted(){
          this.$http
      .get(tools.apiUrl+"occupations")
      .then((response) => {
        console.log(response.data);
        this.lion_type=response.data;
      });
  },
  methods: {
    setwidth() {
      var contextgroup_h;
      var contextgroup_w;
      var contextgroup_bg_w;
      var contextgroup_bg_h;
      var contextgroup_bg_outbox_h;

      var window_bg_h;
      var window_bg_w;
      var window_bg_n_h;
      var window_bg_n_w;
      var window_bg_n_outbox_w;

      if (window.innerWidth < 1439 && window.innerWidth >= 1024) {
        contextgroup_h = (window.innerHeight / 7) * 5;
        contextgroup_w = (window.innerWidth / 9) * 5;
        contextgroup_bg_w = contextgroup_w;
        contextgroup_bg_h = (contextgroup_bg_w * 1106) / 1885;
        contextgroup_bg_outbox_h = (contextgroup_h - contextgroup_bg_h) / 2;

        window_bg_h = (window.innerHeight / 4) * 2;
        window_bg_w = window.innerWidth / 2;
        window_bg_n_h = window_bg_h;
        window_bg_n_w = (window_bg_n_h * 178) / 150;
        window_bg_n_outbox_w = (window_bg_w - window_bg_n_w) / 2;
      } else if (window.innerWidth < 1023 && window.innerWidth >= 768) {
        contextgroup_h = (window.innerHeight / 12) * 10;
        contextgroup_w = (window.innerWidth / 5) * 3;
        contextgroup_bg_w = contextgroup_w;
        contextgroup_bg_h = (contextgroup_bg_w * 1106) / 1885;
        contextgroup_bg_outbox_h = (contextgroup_h - contextgroup_bg_h) / 2;

        window_bg_h = (window.innerHeight / 4) * 2;
        window_bg_w = window.innerWidth / 3;
        window_bg_n_h = window_bg_h;
        window_bg_n_w = (window_bg_n_h * 178) / 150;
        window_bg_n_outbox_w = (window_bg_w - window_bg_n_w) / 2;
      } else if (window.innerWidth < 739 && window.innerWidth >= 720) {
        contextgroup_h = (window.innerHeight / 7) * 5;
        contextgroup_w = (window.innerWidth / 4.5) * 2.5;
        contextgroup_bg_w = contextgroup_w;
        contextgroup_bg_h = (contextgroup_bg_w * 1106) / 1885;
        contextgroup_bg_outbox_h = (contextgroup_h - contextgroup_bg_h) / 2;

        window_bg_h = (window.innerHeight / 4) * 2;
        window_bg_w = window.innerWidth / 2;
        window_bg_n_h = window_bg_h;
        window_bg_n_w = (window_bg_n_h * 178) / 150;
        window_bg_n_outbox_w = (window_bg_w - window_bg_n_w) / 2;
      } else if (window.innerWidth <= 653) {
        contextgroup_h = (window.innerHeight / 20) * 18;
        contextgroup_w = (window.innerWidth / 5) * 3;
        contextgroup_bg_w = contextgroup_w;
        contextgroup_bg_h = (contextgroup_bg_w * 1106) / 1885;
        contextgroup_bg_outbox_h = (contextgroup_h - contextgroup_bg_h) / 2;

        window_bg_h = (window.innerHeight / 4) * 2;
        window_bg_w = window.innerWidth / 3;
        window_bg_n_h = window_bg_h;
        window_bg_n_w = (window_bg_n_h * 178) / 150;
        window_bg_n_outbox_w = (window_bg_w - window_bg_n_w) / 2;
      } else {
        contextgroup_h = (window.innerHeight / 7) * 5;
        contextgroup_w = (window.innerWidth / 9) * 5;
        contextgroup_bg_w = contextgroup_w;
        contextgroup_bg_h = (contextgroup_bg_w * 1106) / 1885;
        contextgroup_bg_outbox_h = (contextgroup_h - contextgroup_bg_h) / 2;

        window_bg_h = (window.innerHeight / 4) * 2;
        window_bg_w = window.innerWidth / 3;
        window_bg_n_h = window_bg_h;
        window_bg_n_w = (window_bg_n_h * 178) / 150;
        window_bg_n_outbox_w = (window_bg_w - window_bg_n_w) / 2;
      }
      console.log(
        window_bg_h,
        window_bg_w,
        window_bg_n_h,
        window_bg_n_w,
        window_bg_n_outbox_w
      );
      if (window_bg_n_outbox_w < 0) {
        this.n_outbox_w = 0;
      } else {
        this.n_outbox_w = window_bg_n_outbox_w;
      }
      if (contextgroup_bg_outbox_h < 0) {
        this.in_outbox_h = 0;
      } else {
        this.in_outbox_h = contextgroup_bg_outbox_h;
      }
    },
    setliontypefun(value) {
      //動態更改梅克獅類型
      this.setliontype = value;
      for (var i = 0; i < this.lion_type.length; i++) {
        var lion_json = this.lion_type[i];
        if (value == lion_json.name) {
          //this.job_type = lion_json.content[0];
          this.occupation_id = lion_json.id;
          this.content = lion_json.description;
        }
      }
    },
    backfun() {
      this.nameshowbool = false;
      document.getElementById("name_input").value = "";
    },
    createlionfun() {
      var name = document.getElementById("name_input").value;
      var boylion = document.getElementById("boy").checked;
      var girllion = document.getElementById("girl").checked;
      console.log(girllion);
      document.getElementById("name_input").value = "";
      console.log(this.occupation_id);
      if (name != "") {
      //新增獅子
        var postdata = {
        "name": name,
        "gender": boylion?"boy":"girl",
        "occupation_id": this.occupation_id,
        "player_id": tools.getCookie("id")
      };
       this.$http.post(tools.apiUrl+"lions",postdata , {emulateJSON: true,headers: {'authorization': 'Bearer '+tools.getCookie("token")}})
   .then(response => {
        this.$parent.togamescenefun(
                    "gamescene",
                    this.setliontype,
                    boylion?"boy":"girl",response.data
                  );
                  console.log(response);
        }, response => {
          this.$parent.alert("網路連線問題，請再試一次!");
          console.log(response);
        });
          
      }else{
        this.$parent.alert("請幫梅克獅取名字!");
      }
    },
  },
};
</script>



