<template>
  <div id="friendpage">
    <div id="friendgroup" v-show="friendlist">
      <div class="flexbox w-100 h-100"></div>
      <div class="friendFrame box w-100 h-100">
        <div class="flexbox w-100 h-100"></div>
        <div class="contentFrame flexbox w-100 h-100 flex-c">
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="h-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
            <div class="flexbox w-100 h-100 flex-c">
              <div class="flexbox w-100 h-100 position-r">
                <div class="back_btn w-100 pointer" @click="friend_back()"></div>
                <div class="close_btn pointer" @click="friend_close(false)"></div>
              </div>
              <div class="w-100 h-100 flexbox">
                <input class="find_bg" type="text" placeholder="搜尋已加入好友" ref="searchinput" id="searchinput" />
                <button type="button" id="searchbutton" @click="searchfun()"></button>
              </div>
              <div class="w-100 h-100 flexbox flex-r">
                <div class="w-100 h-100 flexbox"></div>
                <div class="myFriend h-100 flexbox" v-show="list_rankbool">
                  <input id="listradio" type="radio" name="friendradio" value="list" checked />
                  <label for="listradio" style="width: 100%; height: 100%">
                    <span @click="clicktab('list', '搜尋已加入好友')">我的好友</span>
                  </label>
                </div>
                <div class="ranking h-100 flexbox" v-show="list_rankbool">
                  <input id="rankradio" type="radio" name="friendradio" value="rank" />
                  <label for="rankradio" style="width: 100%; height: 100%">
                    <span @click="clicktab('rank', '增加好友')">排行榜</span>
                  </label>
                </div>
                <div class="w-100 h-100 flexbox"></div>
              </div>
              <div class="findFriend_textbg w-100" v-show="list_rankbool">
                <div id="friendbox" class="w-100 h-100 d-flex flex-r scrollbar"
                  v-bind:style="{ 'max-height': scrollbox_h + 'px' }" v-if="friendscene == 'list'"
                  v-html="friendlistfun()" @click="sendfun"></div>
                <div id="rankbox" class="w-100 h-100 d-flex flex-r scrollbar"
                  v-bind:style="{ 'max-height': scrollbox_h + 'px' }" v-if="friendscene == 'rank'"
                  v-html="ranklistfun(rankarr)"></div>
              </div>
              <div class="w-100 h-60" v-show="searchbool">
                <div id="searchbox" class="w-100 h-100 d-flex flex-r scrollbar justify-center"
                  v-bind:style="{ 'max-height': scrollbox_h + 'px' }" v-html="searchobj" @click="inputsearchfun"></div>
              </div>
              <div class="flexbox w-100 h-100"></div>
            </div>
            <div class="h-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
          </div>

          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>
    <div id="friendsend" v-show="friendsendbool">
      <div class="flexbox w-100 h-100"></div>
      <div class="friendFrame box w-100 h-100">
        <div class="flexbox w-100 h-100"></div>
        <div class="contentFrame flexbox w-100 h-100 flex-c">
          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="w-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
            <div class="flexbox w-100 h-100 flex-c">
              <div class="flexbox w-100 h-100 position-r">
                <div class="close_btn pointer" @click="closefriendsend()"></div>
                <!--關掉贈送禮物介面-->
              </div>
              <div class="chooseGift w-100 h-100 flexbox">選擇贈送禮物...</div>
              <div class="gray_bg w-100 h-100">
                <div class="w-100 h-100 flex-c scrollbar" style="text-align: left"
                  v-bind:style="{ 'max-height': scrollbox_h + 'px' }">
                  <!-- 贈送禮物UI  / 好友贈送 -->

                  <div @click="chooseItem(index, 0)" :class="item.item.data.name + ' senditem'" style="
                      width: 25%;
                      display: inline-block;
                      margin-bottom: 30px;
                    " v-for="(item, index) in sendlist" :key="item.value">
                    <input v-bind:id="index + 'radio'" type="radio" name="meatradio" :value="item.item.data.name" />
                    <label :for="index + 'radio'" class="h-100 flexbox">
                      <span class="position-r">
                        <div class="number_bg" style="text-align: center">
                          {{ item.quantity }}
                        </div>
                      </span>
                    </label>
                  </div>
                  <div @click="chooseItem(index, 1)" :class="item.cloth.data.name + ' senditem'" style="
                      width: 25%;
                      display: inline-block;
                      margin-bottom: 30px;
                    " v-for="(item, index) in sendlistcloth" :key="item.value">
                    <input v-bind:id="sendlist.length + index + 'radio'" type="radio" name="meatradio"
                      :value="item.cloth.data.name" />
                    <label :for="sendlist.length + index + 'radio'" class="h-100 flexbox">
                      <span class="position-r">
                        <div class="number_bg" style="text-align: center">
                          {{ item.quantity }}
                        </div>
                      </span>
                    </label>
                  </div>
                </div>
              </div>
              <div class="flexbox w-100 h-100">
                <div class="flexbox w-100 h-100"></div>
                <div class="flexbox w-100 h-100">
                  <button class="chooseGift_confirm" @click="choosesendfun()">
                    確認
                  </button>
                </div>
                <div class="flexbox w-100 h-100"></div>
              </div>
            </div>
            <div class="w-100" v-bind:style="{ width: outbox_w + 'px' }"></div>
          </div>

          <div class="w-100" v-bind:style="{ height: outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox w-100 h-100"></div>
      </div>
      <div class="flexbox w-100 h-100"></div>
    </div>
    <div id="frienditem" v-show="itembool">
      <div class="flexbox" style="flex: 2"></div>
      <div class="itemFrame flexbox w-100 h-100 flex-c">
        <div class="flexbox"></div>
        <div class="item_frame flexbox w-100 h-100 flex-c">
          <div class="w-100" v-bind:style="{ height: item_outbox_h + 'px' }"></div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="h-100" v-bind:style="{ width: item_outbox_w + 'px' }"></div>
            <div id="itemcontent" class="flexbox w-100 h-100 flex-c" v-html="item(itemtype)" @click="itemscenefun">
            </div>
            <div class="h-100" v-bind:style="{ width: item_outbox_w + 'px' }"></div>
          </div>

          <div class="w-100" v-bind:style="{ height: item_outbox_h + 'px' }"></div>
        </div>
        <div class="flexbox"></div>
      </div>
      <div class="flexbox" style="flex: 2"></div>
    </div>
  </div>
</template>
<script>
import tools from "../lib/tools.js";

export default {
  name: "friend_ship",
  emits: ["friend_close"],
  setup(_, { emit }) {
    function friend_close(value) {
      emit("friend_close", value);
    }

    return { friend_close };
  },
  data() {
    return {
      outbox_h: 20,
      outbox_w: 0,
      scrollbox_h: 380,
      item_outbox_h: 20,
      item_outbox_w: 0,
      friendlist: true, //好友列表
      friendsendbool: false, //好友贈送
      itembool: false, //子物件小視窗
      list_rankbool: true, //好友列表跟排行榜
      searchbool: false, //尋找好友或加好友
      friendscene: "list", //切換好友列表/排行榜
      searchobj: "", //切換尋找好友/加好友
      itemtype: "", //小視窗切換類型
      objname: "牛肉", //贈送物品的名字
      objcss: "beef", //贈送的物品圖片
      beefnum: 20, //牛肉的數量
      chickennum: 20, //雞肉的數量
      pignum: 20, //豬肉的數量
      sendnum: 5, //要贈送的數量
      rankarr: [], //排行榜
      friendarr: [], //朋友
      playerlist: [], //玩家
      addplayer: [], //要加的玩家
      sendlist: [], //可贈送給其他玩家的物品
      sendlistcloth: [], //可贈送給其他玩家的物品
      choosetype: 0,
      chooseindex: 0,
      sendUser: 0,
    };
  },
  mounted() {
    this.refreshView();
  },
  methods: {
    setwidth() {
      var group_h;
      var group_w;
      var group_bg_h;
      var group_bg_w;
      var group_outbox_h;
      var group_outbox_w;
      var scrollbox_h;
      var infoitem_h;
      var infoitem_w;
      var infoitem_bg_h;
      var infoitem_bg_w;
      var infoitem_outbox_h;
      var infoitem_outbox_w;

      if (window.innerWidth > 1920) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = window.innerHeight / 3;
        infoitem_w = window.innerWidth / 5;
      } else if (window.innerWidth >= 1440 && window.innerWidth < 1920) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 1024 && window.innerWidth < 1440) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 768 && window.innerWidth < 1024) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      } else if (window.innerWidth >= 740 && window.innerWidth < 768) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      } else if (window.innerWidth >= 720 && window.innerWidth < 740) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4.5) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 654 && window.innerWidth < 720) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 4) * 2;

        infoitem_h = (window.innerHeight / 4) * 2;
        infoitem_w = (window.innerWidth / 6) * 2;
      } else if (window.innerWidth >= 425 && window.innerWidth < 654) {
        group_h = (window.innerHeight / 5) * 3;
        group_w = (window.innerWidth / 3.5) * 1.5;

        infoitem_h = (window.innerHeight / 4.5) * 2.5;
        infoitem_w = (window.innerWidth / 5.5) * 1.5;
      }

      group_bg_h = (group_w * 150) / 252;
      group_bg_w = (group_h * 252) / 150;
      scrollbox_h = (group_w * 85) / 252;
      group_outbox_w = (group_w - group_bg_w) / 2;
      group_outbox_h = (group_h - group_bg_h) / 2;
      console.log(
        window.innerHeight,
        window.innerWidth,
        group_h,
        group_w,
        group_bg_h,
        group_bg_w,
        group_outbox_h,
        group_outbox_w
      );
      if (group_outbox_h < 0) {
        this.outbox_h = 0;
      } else {
        this.outbox_h = group_outbox_h;
      }
      if (group_outbox_w < 0) {
        this.outbox_w = 0;
      } else {
        this.outbox_w = group_outbox_w;
      }
      this.scrollbox_h = scrollbox_h;

      infoitem_bg_w = (infoitem_h * 178) / 150;
      infoitem_bg_h = (infoitem_w * 150) / 178;
      infoitem_outbox_w = (infoitem_w - infoitem_bg_w) / 2;
      infoitem_outbox_h = (infoitem_h - infoitem_bg_h) / 2;

      console.log(infoitem_outbox_h);
      if (infoitem_outbox_w < 0) {
        this.item_outbox_w = 0;
      } else {
        this.item_outbox_w = infoitem_outbox_w;
      }
      if (infoitem_outbox_h < 0) {
        this.item_outbox_w = 0;
      } else {
        this.item_outbox_h = infoitem_outbox_h;
      }
    },
    closefriendsend() {
      this.friendsendbool = false;
      this.itembool = false;
      this.friendlist = true;
    },
    chooseItem(id, type) {
      this.chooseindex = id;
      this.choosetype = type;
    },
    friend_back() {
      if (this.list_rankbool) {
        this.friend_close(false);
      } else {
        this.list_rankbool = true;
        this.searchbool = false;
        document.getElementById("searchinput").value = "";
      }
    },
    refreshView() {
      //排行榜
      this.$http
        .get(tools.apiUrl + "leaderboard", {
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        })
        .then(
          (response) => {
            this.rankarr.length = 0;
            console.log(response.data);
            this.rankarr = response.data;
          },
          (response) => { }
        );
      //朋友
      this.$http
        .get(tools.apiUrl + "players/" + tools.getCookie("id") + "/friends", {
          headers: { authorization: "Bearer " + tools.getCookie("token") },
        })
        .then(
          (response) => {
            this.friendarr.length = 0;
            console.log(response.data);
            this.friendarr = response.data;
          },
          (response) => { }
        );
      //贈送禮物清單
      this.$http
        .get(
          tools.apiUrl + "players/" + tools.getCookie("id") + "/sendableGifts",
          {
            headers: { authorization: "Bearer " + tools.getCookie("token") },
          }
        )
        .then((response) => {
          this.sendlist = response.data.backpack;
          this.sendlistcloth = response.data.closet;
          console.log("#####", this.sendlistcloth, this.sendlist);
        });
    },
    clicktab(type, content) {
      this.friendscene = type;
      document.getElementById("searchinput").placeholder = content;
    },
    ranklistfun(item) {
      var innerhtml = "";
      for (var i = 0; i < item.length; i++) {
        innerhtml += `
                  <div class="wrapWidth w-100 h-60 d-flex flex-r">
                    <div class="picHeight flex-c">
                      <div
                        class="${this.setrankcss(i)} flexbox h-100"></div>
                      <div class="rankNum_${this.setrankfontcss(i)} flexbox">
                        ${i + 1}
                      </div>
                    </div>
                    <div class="nameText h-100 flexbox flex-c" style="align-items: start;">
                      <p style="margin: 0">${item[i].name}</p>
                      <p class="changLine d-flex" style="margin: 0; font-size: 1.7vmin;">${this.mailnewline(
          item[i].player.account
        )}</p>
                      <p class="stageName" style="margin: 0">LV ${item[i].level
          }</p>
                      <div class="dotPos flexbox">
                        <span class="full"></span>
                        <span class="nofull"></span>
                        <span class="nofull"></span>
                      </div>
                    </div>
                  </div>
        `;
      }
      return innerhtml;
    },
    setrankcss(i) {
      var cssname = "";
      switch (i) {
        case 0:
          cssname = "first";
          break;
        case 1:
          cssname = "second";
          break;
        case 2:
          cssname = "third";
          break;
        default:
          cssname = "other";
          break;
      }
      return cssname;
    },
    setrankfontcss(i) {
      var cssname = "";
      if (i < 3) {
        cssname = "black";
      } else {
        cssname = "white";
      }
      return cssname;
    },
    friendlistfun() {
      var innerhtml = "";
      for (var i = 0; i < this.friendarr.length; i++) {
        innerhtml += `
                  <div class="wrapWidth w-100 h-60 d-flex flex-r">
                    <div class="picHeight flex-c">
                      <div
                        class="lionPic w-100 h-100 flexbox"
                        style="flex: 2"
                      ></div>
                      <div class="sendgiftBtn flexbox" data-id="${this.friendarr[i].id
          }">
                        贈送禮物
                      </div>
                    </div>
                    <div class="nameText h-100 flexbox flex-c" style="align-items: start;">
                      <p style="margin: 0">${this.friendarr[i].lions[0].name
          }</p>
                      <p style="margin: 0">${this.mailnewline(
            this.friendarr[i].account
          )}</p>
                      <p class="stageName" style="margin: 0;">LV ${this.friendarr[i].lions[0].level
          }</p>
                      <div class="dotPos flexbox">
                        <span class="full"></span>
                        <span class="nofull"></span>
                        <span class="nofull"></span>
                      </div>
                    </div>
                  </div>
        `;
      }

      return innerhtml;
    },
    searchfun() {
      //搜尋
      var friendradio = document.getElementsByName("friendradio");
      var inputvalue = document.getElementById("searchinput").value;

      var searchlist = "";
      for (var i = 0; i < friendradio.length; i++) {
        if (friendradio[i].checked) {
          if (friendradio[i].value == "rank") {
            this.$http
              .get(tools.apiUrl + "players", {
                headers: {
                  authorization: "Bearer " + tools.getCookie("token"),
                },
              })
              .then(
                (response) => {
                  console.log("playerslist", response.data);
                  this.playerlist = response.data;
                  for (var j = 0; j < this.playerlist.length; j++) {
                    if (this.playerlist[j].account.indexOf(inputvalue) != -1) {
                      searchlist += `
                      <div class="black_bg d-flex w-100 h-100 flex-c">
                        <div class="addFriend_bg flexbox" style="flex:2;"></div>
                        <div class="nameText flexbox flex-c w-100" style="flex:2;">
                          <p style="margin: 0;white-space: pre-line;">${this.mailnewline(
                        this.playerlist[j].account
                      )}</p>
                        </div>
                        <div class="flexbox w-100">
                          <div class="flexbox w-100 h-100"></div>
                          <div class="addBtn flexbox" style="flex:2;" id="inputaddbtn" data-id="${this.playerlist[j].id
                        }">加入好友</div>
                          <div class="flexbox w-100 h-100"></div>
                        </div>
                      </div>
                    `;
                    }
                  }
                  this.searchobj = searchlist;
                  //  console.log(searchlist);
                  this.list_rankbool = false;
                  this.searchbool = true;
                },
                (response) => {
                  console.log(response);
                }
              );
          } else if (friendradio[i].value == "list") {
            for (var k = 0; k < this.friendarr.length; k++) {
              if (this.friendarr[k].lions[0].name.indexOf(inputvalue) != 1) {
                searchlist += `<div class="findblack_bg flex-c">
          <div class="newFriend_bg flexbox w-100" style="flex:2;"></div>
          <div class="nameText flexbox w-100 flex-c" style="flex:2;">
            <p style="margin: 0">${this.friendarr[k].lions[0].name}</p>
            <p style="margin: 0">${this.mailnewline(
                  this.friendarr[k].account
                )}</p>
            <p style="margin: 0">LV ${this.friendarr[k].lions[0].level}</p>
          </div>
          <div class="flexbox w-100">
            <div class="flexbox w-100 h-100"></div>
            <div class="sendgiftBtn flexbox w-100 h-100" style="flex:2;">贈送禮物</div>
            <div class="flexbox w-100 h-100"></div>
          </div>
        </div>`;
              }
            }
            this.searchobj = searchlist;
            //  console.log(searchlist);
            this.list_rankbool = false;
            this.searchbool = true;
          }
        }
      }
    },
    mailnewline(value) {
      var newline = value.split("@");

      return newline[0] + "<br />@" + newline[1];
    },
    friendgroup(value) {
      var innerhtml = "";
      switch (value) {
        case "search": //尋找好友
          innerhtml = `
        <div class="flexbox w-100 h-100 flex-r">
        <div class="findblack_bg flex-c">
          <div class="newFriend_bg flexbox w-100" style="flex:2;"></div>
          <div class="nameText flexbox w-100 flex-c" style="flex:2;">
            <p style="margin: 0">小明獅</p>
            <p style="margin: 0">LV.51</p>
            <p class="stageName" style="margin: 0">小漁獅-達人</p>
          </div>
          <div class="flexbox w-100">
            <div class="flexbox w-100 h-100"></div>
            <div class="sendgiftBtn flexbox w-100 h-100" style="flex:2;">贈送禮物</div>
            <div class="flexbox w-100 h-100"></div>
          </div>
        </div>
        <div class="findblack_bg flex-c">
          <div class="newFriend_bg flexbox w-100" style="flex:2;"></div>
          <div class="nameText flexbox w-100 flex-c" style="flex:2;">
            <p style="margin: 0">小明獅</p>
            <p style="margin: 0">LV.51</p>
            <p class="stageName" style="margin: 0">小漁獅-達人</p>
          </div>
          <div class="flexbox w-100">
            <div class="flexbox w-100 h-100"></div>
            <div class="sendgiftBtn flexbox w-100 h-100" style="flex:2;">贈送禮物</div>
            <div class="flexbox w-100 h-100"></div>
          </div>
        </div>
        <div class="findblack_bg flex-c">
          <div class="newFriend_bg flexbox w-100" style="flex:2;"></div>
          <div class="nameText flexbox w-100 flex-c" style="flex:2;">
            <p style="margin: 0">小明獅</p>
            <p style="margin: 0">LV.51</p>
            <p class="stageName" style="margin: 0">小漁獅-達人</p>
          </div>
          <div class="flexbox w-100">
            <div class="flexbox w-100 h-100"></div>
            <div class="sendgiftBtn flexbox w-100 h-100" style="flex:2;">贈送禮物</div>
            <div class="flexbox w-100 h-100"></div>
          </div>
        </div>
        </div>
        
        `;
          break;
        case "add": //新增好友
          innerhtml = `
        <div class="flexbox w-100 h-100 flex-r">
        <div class="flexbox w-100 h-100 flex-c"></div>
        <div class="black_bg lexbox w-100 h-100 flex-c">
          <div class="addFriend_bg flexbox" style="flex:2;"></div>
          <div class="nameText flexbox flex-c w-100" style="flex:2;">
            <p style="margin: 0">新加獅</p>
            <p style="margin: 0">LV.54</p>
            <p class="stageName" style="margin: 0">小漁獅-達人</p>
          </div>
          <div class="flexbox w-100">
            <div class="flexbox w-100 h-100"></div>
            <div class="addBtn flexbox" style="flex:2;" id="inputaddbtn">加入好友</div>
            <div class="flexbox w-100 h-100"></div>
          </div>
        </div>
        <div class="flexbox w-100 h-100 flex-c"></div>
        </div>
        
        `;
          break;
      }

      return innerhtml;
    },
    sendfun(e) {
      // console.log(e.target.classList);
      var clickedElClassList = e.target.classList;
      for (var i = 0; i < clickedElClassList.length; i++) {
        if (clickedElClassList[i] == "sendgiftBtn") {
          this.friendlist = false;
          this.friendsendbool = true;
          this.sendUser = e.target.getAttribute("data-id");
        }
      }
    },
    inputsearchfun(e) {
      console.log(e.target.id);
      var clickedElId = e.target.id;
      switch (clickedElId) {
        case "inputaddbtn":
          var value = e.target.dataset.id;
          for (var i = 0; i < this.playerlist.length; i++) {
            if (this.playerlist[i].id == value) {
              this.addplayer = this.playerlist[i];
            }
          }
          this.itemtype = "addfriend";
          this.itembool = true;

          // this.list_rankbool=true;
          // this.searchbool=false;
          break;
        case "":
          break;
      }
      // this.list_rankbool = true;
      // this.searchbool = false;
      document.getElementById("searchinput").value = "";
    },
    choosesendfun() {
      //var selectedGender;

      if (this.choosetype == 0) {
        this.objcss = this.sendlist[this.chooseindex].item.data.name;

        this.objname = this.sendlist[this.chooseindex].item.name;
      } else {
        this.objcss = this.sendlistcloth[this.chooseindex].cloth.data.name;
        this.objname = this.sendlistcloth[this.chooseindex].cloth.name;
      }

      this.itembool = true;
      this.itemtype = "send";
    },
    item(value) {
      var innerhtml = "";
      switch (value) {
        case "addfriend": //加入好友
          innerhtml = `
        <div class="flexbox w-100 h-100">
        </div>
        <div class="flexbox w-100 h-100 flex-r" style="flex:3;">
          <div class="lionIcon flexbox w-100 h-100"></div>
          <div class="dataText flexbox w-100 h-100 flex-c">
            <p>${this.mailnewline(this.addplayer.account)}</p>
          </div>
        </div>
        <div class="flexbox w-100 h-100 flex-r">
          <div class="flexbox w-100 h-100">
            <button class="cancelBtn" id="cancelBtn">取消</button>
          </div>
          <div class="flexbox w-100 h-100">
            <button class="addBtn" id="addBtn">加入好友</button>
          </div>
        </div>
        `;
          break;
        case "sendsuccess": //成功贈送
          innerhtml = `
        <div class="flexbox w-100 h-100"></div>
        <div class="flexbox w-100 h-100 flex-c" style="flex:3;">
          <div class="flexbox w-100 h-100" style="flex:2;">
            <div class="flexbox w-100 h-100"></div>
            <div class="itemPos flexbox w-100 h-100 flex-c position-r">
              <div class="itemPic_${this.objcss} w-100 h-100"></div>
              <div class="num_bg">${this.sendnum}</div>
            </div>
            <div class="flexbox w-100 h-100"></div>
          </div>
          <div class="sendSuccess flexbox w-100 h-100">
            <p>成功贈送</p>
          </div>
        </div>
        <div class="sendSuccess_confirm flexbox w-100 h-100">
            <button id="success_checkbtn">確認</button>
        </div>
        `;
          break;
        case "send": //贈送物品
          innerhtml = `
        <div class="flexbox w-100"></div>
        <div class="itemName flexbox w-100 h-100">
          ${this.objname}
        </div>
        <div class="flexbox w-100 h-100 flex-c" style="flex:3;">
          <div class="flexbox w-100 h-100" style="flex:2;">
            <div class="flexbox w-100 h-100"></div>
            <div class="itemPic_${this.objcss
            } flexbox w-100 h-100 flex-c position-r">
              <div class="w-100 h-100"></div>
              <div class="num_bg">${this.objnumfun()}</div>
            </div>
            <div class="flexbox w-100 h-100"></div>

          </div>
          <div class="flexbox w-100 h-100 flex-r">
            <div class="flexbox w-100 h-100"></div>
            <div class="posSet flexbox w-100 h-100">
              <input type="button" value="" class="minus" id="minusnum">
              <input type="number" id="friendsendnum" step="1" min="1" max="${this.objnumfun()}" name="quantity" value="1" pattern="" inputmode="">
              <input type="button" value="" class="plus" id="plusnum">
            </div>
            <div class="flexbox w-100 h-100"></div>
          </div>
        </div>
        <div class="flexbox w-100 h-100">
          <div class="flexbox w-100 h-100">
            <button class="cancelBtn" id="send_cancel">取消</button>
          </div>
          <div class="flexbox w-100 h-100">
            <button class="sendBtn" id="send_send">贈送</button>
          </div>
        </div>
        `;
          break;
      }

      return innerhtml;
    },
    objnumfun() {
      if (this.choosetype == 0) {
        return this.sendlist[this.chooseindex].quantity;
      } else {
        return this.sendlistcloth[this.chooseindex].quantity;
      }
    },
    itemscenefun(e) {
      var clickedElId = e.target.id;
      console.log(clickedElId);
      switch (clickedElId) {
        case "addBtn":
          var postdata = {
            player: tools.getCookie("id"),
            receiver: this.addplayer.id,
          };
          this.$http
            .post(
              tools.apiUrl +
              "players/" +
              tools.getCookie("id") +
              "/friendInvitation/" +
              this.addplayer.id,
              postdata,
              {
                headers: {
                  authorization: "Bearer " + tools.getCookie("token"),
                },
              }
            )
            .then(
              (response) => {
                console.log(response.data);
                this.itembool = false;
                this.list_rankbool = true;
                this.searchbool = false;
              },
              (response) => {
                console.log(response);
              }
            );
          break;
        case "cancelBtn":
          this.itembool = false;
          this.list_rankbool = true;
          this.searchbool = false;
          break;
        case "plusnum": //增加數量
          document.getElementById("friendsendnum").stepUp();
          //document.getElementById("sendnum").value+=1;
          console.log(document.getElementById("friendsendnum").value);
          break;
        case "minusnum": //減少數量
          document.getElementById("friendsendnum").stepDown();
          console.log(document.getElementById("friendsendnum").value);
          break;
        case "send_cancel": //取消
          document.getElementById("friendsendnum").value = 1;
          this.itembool = false;
          break;
        case "send_send": //贈送
          this.sendnum = document.getElementById("friendsendnum").value;
          var pdd = null;
          if (this.choosetype == 1) {
            pdd = {
              closet_id: this.sendlistcloth[this.chooseindex].id,
              quantity: this.sendnum,
            };
          } else {
            pdd = {
              backpack_id: this.sendlist[this.chooseindex].id,
              quantity: this.sendnum,
            };
          }
          this.$http
            .post(
              tools.apiUrl +
              "players/" +
              tools.getCookie("id") +
              "/sendGift/" +
              this.sendUser,
              pdd,
              {
                headers: {
                  authorization: "Bearer " + tools.getCookie("token"),
                },
              }
            )
            .then(
              (response) => {
                console.log(response.data);
                this.itemtype = "sendsuccess";
                document.getElementById("friendsendnum").value = 1;
              },
              (response) => {
                console.log(response);
              }
            );

          break;
        case "success_checkbtn":
          this.itembool = false;
          this.friendlist = true;
          this.friendsendbool = false;
          break;
      }
    },
  },
};
</script>
