  <!-- 右側按鈕 -->
<template>
  <div id="btn_group">
    <div class="setBtn" id="btn_set" @click="setting_btn(true)">
      <!-- <img src="@/assets/button-01.svg" alt="setting" /> -->
    </div>
    <div class="mailBtn" id="btn_mail" v-if="loginbool" @click="info_btn(true)">
      <!-- <img src="@/assets/button-02.svg" alt="mail" /> -->
      <div id="redDot" v-show="infodotbool"></div>
    </div>
    <div
      class="personBtn"
      id="btn_person"
      v-if="loginbool"
      @click="friend_btn(true)"
    >
      <!-- <img src="@/assets/button-03.svg" alt="person" /> -->
    </div>
    <div class="giftBtn" id="btn_gift" v-if="loginbool" @click="gift_btn(true)">
      <!-- <img src="@/assets/giftbox_btn-01.svg" alt="gift" /> -->
      <div id="redDot" v-show="giftboxdotbool"></div>
    </div>
    <div class="shareBtn" id="btn_share" v-if="loginbool" @click="share_btn(true)">
      <!-- <img src="@/assets/camera_btn-01.svg" alt="share"> -->
    </div>
    <div
      class="logOutBtn"
      id="btn_logOut"
      v-if="loginbool"
      @click="log_out('login')"
    >
      <!-- <img src="@/assets/button-04.svg" alt="logOut" /> -->
    </div>
  </div>
</template>

<script>

export default {
  name: "right_menu",
  emits: [
    "setting_btn",
    "info_btn",
    "friend_btn",
    "gift_btn",
    "share_btn",
    "btn_group",
    "log_out",
  ],
  setup(_, { emit }) {
    function setting_btn(value) {
      emit("setting_btn", value);
    }
    function info_btn(value) {
      emit("info_btn", value);
    }
    function friend_btn(value) {
      emit("friend_btn", value);
    }
    function gift_btn(value) {
      emit("gift_btn", value);
    }
    function share_btn(value) {
      emit("share_btn", value);
    }
    function btn_group(value) {
      emit("btn_group", value);
    }
    function log_out(value) {
      emit("log_out", value);
    }
    return { setting_btn, info_btn, friend_btn, gift_btn,share_btn ,btn_group, log_out };
  },
  data() {
    return {
      loginbool: false,
      infodotbool: false,
      giftboxdotbool: false,
    };
  },

  methods: {
    showornotbtn(value) {
      this.loginbool = value;
      console.log(this.loginbool);
    }
  },
};
</script>

